// /* global d3 $ */
import DrawDynamic from './drawDynamic.js'
// import dataService from '../../service/dataService.js'
// import pipeService from '../../service/pipeService'

export default {
    name: 'DynamicView',
    components: {
    },
    props: {
        videoId: String,
        videoData: Object
    },
    data() {
        return {
            containerId: 'dynamicContainer',
            audioData: {},
            interval: 5,
            sliding: 1,
            dynamicType: 'animation'
        }
    },
    watch: {
        videoData: function (videoData) {
            this.drawDynamic.layout(videoData)
        }
    },
    mounted: function () {
        this.drawDynamic = new DrawDynamic(this.containerId)
    }
}
