DrawMatrix.prototype.drawTimelineTwo = function (movementData) {
    // console.log('drawTimelineTwo, movementData: ', movementData)
    this.timelineTwoContainer.select('.drawTimelineTwo').remove()
    let container = this.timelineTwoContainer.append('g')
        .attr('class', 'drawTimelineTwo')
    const _this = this
    // console.log('drawTimelineTwo _this: ', _this)
    // let frameGap = 1
    // let sampleFrameList = d3.range(0, this.videoInfo['totalFrmNum'], frameGap)
    
    let width = this.timelineTwoWidth
    let height = this.timelineTwoHeight
    let clickAreaWidth = 20
    let markAreaWidth = 15
    let markSize = 10

    let xScale = this.gestureSpaceXScale

    let yScale = d3.scaleLinear()
        .domain([0, this.videoInfo['totalFrmNum']])
        .range([0, height])
    this.timelineTwoYScale = yScale

    ////////////////////////////////
    // draw gesture space vertical line
    let centerPoint = globalConfig.centerPoint
    container.append('g')
        .attr('class', 'gesture-space-vertical-line')
        .append('line')
        .attr('x1', xScale(centerPoint[0]))
        .attr('y1', 0)
        .attr('x2', xScale(centerPoint[0]))
        .attr('y2', height)
        .style('stroke', 'black')
        .style('stroke-dasharray', '2,2')
        .style('stroke-linecap', 'butt')
        .style('stroke-width', '1')


    ////////////////////////////////
    // draw leftHand, rightHand X data  
    let selectedPoseDataList = movementData['selectedPoseDataList']  

    ////////////
    // left hand line
    let leftHandLine = d3.line()
        .x(function(d) { return xScale(d['leftHand'][0]) }) 
        .y(function(d) { return yScale(d['frameIdx']) })
        .defined(function(d) {
            return d['leftHand'].length !==0
        })
        .curve(d3.curveMonotoneY)

    let rightHandLine = d3.line()
        .x(function(d) { return xScale(d['rightHand'][0]) }) 
        .y(function(d) { return yScale(d['frameIdx']) })
        .defined(function(d) {
            return d['rightHand'].length !==0
        })
        .curve(d3.curveMonotoneY)

    container.append('path')
        .attr('class', 'timelineTwo-leftHnad')
        .datum(selectedPoseDataList)
        .attr('class', 'line')
        .attr('d', leftHandLine)
        .style('stroke', globalConfig.leftHandColor)
        .style('fill', 'none')
    
    container.append('path')
        .attr('class', 'timelineTwo-rightHnad')
        .datum(selectedPoseDataList) 
        .attr('class', 'line')
        .attr('d', rightHandLine)
        .style('stroke', globalConfig.rightHandColor)
        .style('fill', 'none')

    // add the y Axis
    let yAxis = d3.axisLeft(yScale)
        .tickFormat(d => {
            if (d==0) {
                return ''
            } else {
                return this.formatMinutes(d / this.fps)
            }
        })
        .tickValues(d3.range(0, this.totalFrameNum, this.axisTimeRange))
    container.append('g')
        .attr('transform', 'translate(' + width + ',' + 0 + ')')
        .call(yAxis)
        // .call(d3.axisLeft(yScale))

    ///////////////////////////////////////////////////////
    // frame line
    container.select('.timelineTwo-selected-frame').remove()
    let yPosition = yScale(this.selectedFrameIdx)
    this.frameLineTwo = container.append('g')
        .attr('class', 'timelineTwo-selected-frame')
        .append('path')
        .attr('class', 'frame-line-two')
        .attr('d', () => {
            let d = 'M' + (width-clickAreaWidth) + ',' + yPosition
            d += ' ' + (markAreaWidth+markSize) + ',' + yPosition
            return d
        })
        .style('stroke', 'black')
        .style('stroke-width', '1px')
        .style('vector-effect', 'non-scaling-stroke')

    // brush interaction
    let brush = d3.brushY()
        .extent([[markAreaWidth+markSize, 0], [width-clickAreaWidth, height]])
        .on('end', brushed)

    container.append('g')
        .attr('class', 'brushY')
        .call(brush)

    function brushed() {
        if (!d3.event.sourceEvent) return // Only transition after input.
        let selectionBisect = []
        if (d3.event.selection) {
            // let s = d3.event.selection
            let selection = d3.event.selection.map(yScale.invert)
            selectionBisect = [Math.ceil(selection[0]), Math.floor(selection[1])]
            pipeService.emitUpdateTextList([selectionBisect])
            pipeService.emitUpdateTimeRange(selectionBisect)
            pipeService.emitHighlightText(selectionBisect)
            pipeService.emitHighlightPoint(selectionBisect)
        } else {
            pipeService.emitUpdateTextList([])
            pipeService.emitUpdateTimeRange([0, 0]) // a bug here
            pipeService.emitHighlightText([])
            pipeService.emitHighlightPoint([])
        }
        pipeService.emitFrameBrush(selectionBisect)
        // To do:
        // brush and highligh projection circles
    }

    // add clickArea for jumping to video
    let clickArea = container.append('g')
        .attr('class', 'clickArea')
        .attr('transform', 'translate(' + (width-clickAreaWidth) + ',' + 0 + ')')
        .append('rect')
        .attr('width', clickAreaWidth)
        .attr('height', height)
        .style('opacity', 0.05)
        .style('fill', 'D6D6D6')

    clickArea.on('click', function () {
        let mouse = d3.mouse(this)
        let _arrayIdx = Math.round(yScale.invert(mouse[1]))
        let arrayIdx = _this.bisect(_this.frameIdxList, _arrayIdx)
        _this.selectedFrameIdx = arrayIdx
        // console.log('_this.selectedFrameIdx: ', _this.selectedFrameIdx)
        pipeService.emitFrameSelectedFromTimeBar(_this.selectedFrameIdx)
        pipeService.emitUpdateTimeFrame(_this.selectedFrameIdx)
    })

    // add gesture phase marker
    let markArea = container.append('g')
        .attr('class', 'markArea')
        .attr('transform', 'translate(' + 0 + ',' + 0 + ')')
    
    markArea.append('rect')
        .attr('width', markAreaWidth)
        .attr('height', height)
        .style('opacity', 0.05)
        .style('fill', 'D6D6D6')
        // .style('opacity', 0.1)
        // .style('fill', 'red')
    
    pipeService.onUpdateTimelineMarker((selectedTimeRanges) => {
        let markData = {
            'selectedTimeRanges': selectedTimeRanges,
            'yScale': yScale,
            'areaMarkWidth': markAreaWidth
        }
        this.drawTimelineTwoMarker(markArea, markData)
    })
    
    // frameLine from videoview
    pipeService.onFrameSelectedFromVideo((arrayIdx) => {
        this.selectedFrameIdx = arrayIdx
        let yPosition = this.timelineTwoYScale(this.selectedFrameIdx)
        this.frameLineTwo.transition()
            .duration(200)
            .attr('d', () => {
                let d = 'M' + (width-clickAreaWidth) + ',' + yPosition
                d += ' ' +  (markAreaWidth+markSize) + ',' + yPosition
                return d
            })
    })

    // frameLine from timebar
    pipeService.onUpdateTimeFrame((arrayIdx) => {
        this.selectedFrameIdx = arrayIdx
        let yPosition = this.timelineTwoYScale(this.selectedFrameIdx)
        this.frameLineTwo.transition()
            .duration(200)
            .attr('d', () => {
                let d = 'M' + (width-clickAreaWidth) + ',' + yPosition
                d += ' ' + (markAreaWidth+markSize) + ',' + yPosition
                return d
            })
    })

    // time range from timebar
    pipeService.onUpdateTimeRange((selection) => {
        // console.log('two, onUpdateTimeRange: ', selection)
        if (selection.length == 0) {
            d3.select('.brushY').call(brush.move, null)
        } else {
            d3.select('.brushY').transition().call(brush.move, selection.map(this.timelineTwoYScale))
        }
    })
}

