DrawMatrix.prototype.drawTextList = function (frameRangeSelections) {
    const _this = this
    let textListSelection = []
    for (let i=0; i<frameRangeSelections.length; i++) {
        frameRangeSelections[i].push(i)
        textListSelection.push(frameRangeSelections[i])
        textListSelection.push(frameRangeSelections[i])
    }
    // console.log('drawTextList: textListSelection', textListSelection)
    let gesturePhaseSentenceData = []
    for (let i=0; i<frameRangeSelections.length; i++) {
        let selectedSentence = []
        let frameRange = frameRangeSelections[i]
        let phaseStartTime = parseFloat((frameRange[0]/this.fps).toFixed(2))
        let phaseEndTime = parseFloat((frameRange[1]/this.fps).toFixed(2))
        for (let j=0; j<this.sentenceData.length; j++) {
            let sentenceStartTime = parseFloat(this.sentenceData[j][0]['start_time'])
            let sentenceEndTime = parseFloat(this.sentenceData[j][this.sentenceData[j].length-2]['end_time'])
            if (phaseStartTime>sentenceEndTime) continue
            if (phaseEndTime<sentenceStartTime) continue
            selectedSentence = selectedSentence.concat(this.sentenceData[j])
        }
        gesturePhaseSentenceData.push(selectedSentence)
    }

    // console.log('gesturePhaseSentenceData: ', gesturePhaseSentenceData)
    let sentenceWordHighlightList = []
    let sentenceWordHighlightDict = []
    for (let i=0; i<gesturePhaseSentenceData.length; i++) {
        let wordIndexList = []
        let wordIndexDict = {}
        let sentence = gesturePhaseSentenceData[i]
        let frameRange = frameRangeSelections[i]
        // let phaseStartTime = parseFloat((frameRange[0]/this.fps).toFixed(2))
        // let phaseEndTime = parseFloat((frameRange[1]/this.fps).toFixed(2))
        let phaseStartTime = frameRange[0]/this.fps
        let phaseEndTime = frameRange[1]/this.fps
        for (let j=0; j<sentence.length; j++) {
            let wordItem = sentence[j]
            let wordStartTime = parseFloat(wordItem['start_time'])
            let wordEndTime = parseFloat(wordItem['end_time'])
            let wordDuration = wordEndTime - wordStartTime
            wordItem['highlight'] = false
            if ((wordStartTime >= (phaseStartTime-wordDuration)) && ((phaseEndTime+wordDuration) >= wordEndTime)) {
                wordItem['highlight'] = true
                wordIndexList.push(j)
                wordIndexDict[j] = 1
                
            }
        }
        sentenceWordHighlightList.push(wordIndexList)
        sentenceWordHighlightDict.push(wordIndexDict)
    }
    // console.log('sentenceWordHighlight: ', sentenceWordHighlight)

    // draw text List
    // collpase: https://getbootstrap.com/docs/4.3/components/collapse/
    let textListWidth = this.matrixWidth-20
    this.textContainer.selectAll('div').remove()
    this.textContainer.selectAll('div')
        .data(textListSelection)
        .enter()
        .append('div')
        .attr('class', (_, i) => {
            if (i%2==0) {
                return 'speech-content ' + 'selected-content-' + Math.floor(i/2).toString()
            } else {
                return 'speech-feature ' + 'selected-feature-' + Math.floor(i/2).toString() + ' collapse'
            }
        })
        .style('width', textListWidth+'px')
        .style('height', (_, i) => {
            if (i%2==0) {
                return '80px'
            } else {
                return '100px'
            }
        })
        .style('background-color', (_, i) => {
            if (i%2==0) {
                return '#e0e0e0'
            } else {
                return '#bababa'
            }
        })
        .style('margin', '10px 20px 10px 10px')

     d3.selectAll('.speech-content').each(splitArea)

    function splitArea(_, i) {
        let areaList = ['actionArea', 'textArea']
        d3.select(this).selectAll('div')
            .data(areaList)
            .enter()
            .append('div')
            .attr('class', (d) => `${d}-${i} ${d}`)
            .style('display', 'inline-block')
            .style('overflow-x', (d) => {
                if (d=='textArea') {
                    return 'auto'
                } else {
                    return 'hidden'
                }
            })
            .style('white-space', 'nowrap')
            .style('width', (d) => {
                if (d == 'textArea') {
                    return '98%'
                } else {
                    return '2%'
                }
            })
            .style('height', '100%')
            .style('background-color', (d) => {
                if (d=='textArea') {
                    return '#e0e0e0'
                } else {
                    // return '#5aae61'
                    return '#75C791'
                }
            })
        // add interaction for action area
        d3.select(this).selectAll('div.actionArea')
            .on('click', handleActionAreaMouseClick)

        d3.select(this).selectAll('div.textArea')
            .on('click', handleTextAreaMouseClick)
    }

    function handleActionAreaMouseClick() {
        // console.log('click')
        // console.log('this: ', this)
        // console.log('d3.select(this): ', d3.select(this))
        // console.log(d3.select(this).node().parentNode)
        // console.log('d3.select(this.parentNode).datum(): ', d3.select(this.parentNode).datum())
        let idx = d3.select(this.parentNode).datum()[2]
        // https://clubmate.fi/javascript-adding-and-removing-class-names-from-elements/
        // if (i%2 !== 0) return
        // console.log('id: ', i, 'selected-feature-' + Math.floor(i/2).toString())
        let el = document.getElementsByClassName('selected-feature-' + idx.toString())[0]
        if (el.classList.contains('show')) {
            // Has show in it
            el.classList.remove('show')
        } else {
            // No show:(
            el.classList.add('show')
        }
    }

    function handleTextAreaMouseClick() {
        let idx = d3.select(this.parentNode).datum()[2]
        // https://clubmate.fi/javascript-adding-and-removing-class-names-from-elements/
        // console.log('idx, frameRangeSelections[idx]: ', idx, frameRangeSelections[idx])
        let timeRange = frameRangeSelections[idx]
        pipeService.emitClickFromTextlist(timeRange)

        // let timeTimeRangeToDynamic = frameRangeSelections[idx]
        // let timeTimeRangeToVideo = {
        //     'phaseIdx': frameRangeSelections[idx][2],
        //     'startFrameIdx': frameRangeSelections[idx][0],
        //     'endFrameIdx': frameRangeSelections[idx][1]
        // }
        // pipeService.emitFrameBrush(timeTimeRangeToDynamic)        
        // pipeService.emitSeekFromPhase(timeTimeRangeToVideo)
    }

    // draw text
    d3.selectAll('.listStickman').remove()
    d3.selectAll('.speech-content').selectAll('.textArea').each(drawText)
    function drawText() {
        // https://stackoverflow.com/questions/47911669/forcing-horizontal-scrollbar-and-all-child-divs-in-one-line
        let idx = d3.select(this.parentNode).datum()[2]
        let wordDiv = d3.select(this).selectAll('div')
            .data(gesturePhaseSentenceData[idx])
            .enter()
            .append('div')
            .style('display', 'inline-block')
            .style('margin', '5px 3px')
            
            // .style('border-color', 'red')
            .style('border', (_, i) => {
                if (i in sentenceWordHighlightDict[idx]) {
                    return '1px solid rgba(255, 0, 0, 1)'
                } else {
                    return '1px solid rgba(255, 0, 0, 0)'
                }
            })


            // word
            wordDiv.each(drawEachWord)

            function drawEachWord (d, i) {
                d3.select(this).append('div')
                    .attr('class', () => `sentence-${idx}-word-${i}`)
                    .style('user-select', 'none')
                    .text(d.word)
                    .style('color', () => {
                        if (d.polarity > 0.1) {
                            return 'red'
                        } else if (d.polarity < -0.1) {
                            return 'blue'
                        }
                    })
            }

            // gesture
            let stickmanGraphWidth = 40
            let stickmanGraphHeight = 40
            let gestureSpaceXScale = d3.scaleLinear()
                .domain([globalConfig.gestureSpace.extremePeriphery.left, globalConfig.gestureSpace.extremePeriphery.left+globalConfig.gestureSpace.extremePeriphery.width])
                .range([0, stickmanGraphWidth])

            let gestureSpaceYScale = d3.scaleLinear()
                .domain([globalConfig.gestureSpace.extremePeriphery.top, globalConfig.gestureSpace.extremePeriphery.top+globalConfig.gestureSpace.extremePeriphery.height])
                .range([0, stickmanGraphHeight])
            if (_this.showGesture) {
                wordDiv.each(drawStickman)
            }
            wordDiv.each(drawStickman)

            function drawStickman (d, i) {
                if (d['type'] === 'punctuation') return
                if (!d['jointData'] || d['jointData'].length == 0) return
                let jointData = d['jointData'].filter((d) => d.length!=0)
                if (jointData.length === 0) return
                // console.log('idx, i, `.sentence-${idx}-word-${i}`, ', idx, i, $(`.sentence-${idx}-word-${i}`), $(`.sentence-${idx}-word-${i}`).outerWidth())
                // let wordWidth = $(`.sentence-${idx}-word-${i}`).outerWidth()
                let wordWidth = 42
                let stickmanSvgWidth = 42
                let stickmanSvgHeight = 40
                // let stickmanDiv = d3.select(this).append('div')
                
                d3.select(this).selectAll('.listStickman').remove()
                let stickmanDiv = d3.select(this).insert('div',':first-child')
                    .attr('class', `stickman-${i} stickman listStickman`)
                    .style('width', `${wordWidth}px`)
                    .style('height', `${stickmanSvgHeight}px`)
                    // .style('margin', '10px 0px -15px 0px')
                    .style('cursor', 'pointer')
                    .style('display', function() {
                        if (_this.showGesture) {
                            return 'block'
                        } else {
                            return 'none'
                        }
                    })
                    .on('click', function() {
                        let wordStartTime = parseFloat(d['start_time'])
                        let wordEndTime = parseFloat(d['end_time'])
                        let wordStartFrame = Math.max(0, Math.floor(wordStartTime * _this.fps))
                        let wordEndFrame = Math.min(_this.totalFrameNum, Math.ceil(wordEndTime * _this.fps))
                        pipeService.emitSeekFromPhase({
                            'startFrameIdx': wordStartFrame,
                            'endFrameIdx': wordEndFrame
                        })
                    })

                let stickmanSvg = stickmanDiv.append('svg')
                    .style('width', stickmanSvgWidth)
                    .style('height', stickmanSvgHeight)
                    
                
                let xOffset = (stickmanSvgWidth-stickmanGraphWidth)/2
                let yOffset = (stickmanSvgHeight-stickmanGraphHeight)/2
                let stickmanMargin = {top: yOffset+10, right: xOffset, bottom: yOffset, left: xOffset}

                let stickmanGraph = stickmanSvg.append('g')
                    .attr('class', 'stickmanGraph')
                    .attr('transform', 'translate(' + stickmanMargin.left + ', ' + stickmanMargin.top + ')')
                
                let oneStickmanContainer = stickmanGraph.append('g')
                    .attr('class', 'stickmans')

                oneStickmanContainer.selectAll('.oneStickman')
                    .data(jointData)
                    .enter()
                    .append('g')
                    .attr('class', 'oneStickman')
                    .each(drawOneStickman)

                function drawOneStickman(jointPoints) {
                    
                    d3.select(this).append('g')
                        .attr('class', 'body-point-connection')
                        .selectAll('line')
                        .data(globalConfig.UPBODYPairs)
                        .enter()
                        .append('line')
                        .attr('x1', d => gestureSpaceXScale(jointPoints[d[0]][0]))
                        .attr('y1', d => gestureSpaceYScale(jointPoints[d[0]][1]))
                        .attr('x2', d => gestureSpaceXScale(jointPoints[d[1]][0]))
                        .attr('y2', d => gestureSpaceYScale(jointPoints[d[1]][1]))
                        .style('stroke', '#bdbdbd')
                        .style('stroke-width', 1.5) // original one is 4
                        .style('opacity', 0.5)
                }
            }
    }

    // initialize scroll position
    d3.selectAll('.speech-content').selectAll('.textArea').each(initializeScrollPosition)

    function initializeScrollPosition () {
        let idx = d3.select(this.parentNode).datum()[2]
        if (sentenceWordHighlightList[idx].length == 0) return
        let highlightWordIdx = sentenceWordHighlightList[idx][0]
        let firstHighlightWordOffsetLeft = d3.select(`.sentence-${idx}-word-${highlightWordIdx}`).node().offsetLeft
        // console.log('firstWordOffsetLeft: ', firstHighlightWordOffsetLeft)
        d3.select(`.textArea-${idx}`).node().scrollLeft = (firstHighlightWordOffsetLeft-20)
    }
}

DrawMatrix.prototype.genSentenceData = function (data) {
    let wordData = data
    let sentenceData = []
    let oneSentence = []
    for (let i=0; i<wordData.length; i++) {
        let wordItem = wordData[i]
        wordItem['sentenceIdx'] = sentenceData.length
        oneSentence.push(wordItem)
        if (wordItem['type'] === 'punctuation') {
            sentenceData.push(oneSentence)
            oneSentence = []
        }
        if (i===wordData.length-1 && oneSentence.length!==0) {
            sentenceData.push(oneSentence)
        }
    }
    return sentenceData
}


