            // console.log('in d3.custom.brush, d', d)
            d.on('mousedown', function(){brushStart(this, event)})
                // .on('mousemove', function(){brushDrag(this, event)})
                .on('mouseup', function(){brushStop(this, event)})
            d3.select(document).on('mouseup', function(){brushStop(this, event)})
