import DrawVideo from './drawVideo.js'
// import dataService from '../../service/dataService.js'
import pipeService from '../../service/pipeService.js'

export default {
    name: 'VideoView',
    components: {
    },
    props: {
        videoList: Array,
        videoId: String,
        videoData: Object
    },
    data() {
        return {
            videoUrlList: [],
            selectedVideo: '',
            poseData: {},
            frameIdx: 0,
            ReviewerNameFlag: true
        }
    },
    watch: {
        selectedVideo: function (selectedVideo) {
            this.selectedVideo = selectedVideo
            // console.log('this.selectedVideo: ', this.selectedVideo)
            if (this.selectedVideo !== '') {
                pipeService.emitSelectVideo(this.selectedVideo)
            }
        },
        videoData: function (videoData) {
            // let videoUrl = `${dataService.dataServerUrl}/video/${this.videoId}`
            let videoUrl = `/data/${this.videoId}.mp4`
            this.drawVideo.layout('#labelsContainer', videoData, videoUrl)
        },
        frameIdx: function (frameIdx) {
            this.drawVideo.updateDrawingLabelPoses(frameIdx)
        }
    },
    methods: {
    },
    mounted: function () {
        this.drawVideo = new DrawVideo('#labelsContainer')
    }
}
