<template>
  <div id="app" style="width: 1400px">
      <nav class="navbar sticky-top navbar-dark bg-dark" style="padding-top: 1px; padding-bottom: 1px; margin-bottom: 5px;">
          <div style="margin-top:5px; margin-left: 15px;">
              <span style="color:white; font-size:1.25rem; font-weight:500; user-select: none">GestureLens: Visual Analysis of Gesture in Presentation Videos</span>
          </div>
      </nav>
      <div class="container-fluid" style="padding-left: 5px; padding-right: 5px">
          <div class="row" style="margin-left: 10px; margin-right: 10px">
              <div class="col-3 content" style="padding-left: 3px; padding-right: 3px">
                <RelationView :videoId="videoId" :videoData="videoData"></RelationView>
              </div>
              <div class="col-6 content" style="padding-left: 3px; padding-right: 3px">
                <MatrixView :videoId="videoId" :videoData="videoData"></MatrixView>             
              </div>
              <div class="col-3 content" style="padding-left: 3px; padding-right: 3px">
                <VideoView :videoList="videoList" :videoId="videoId" :videoData="videoData"></VideoView>
                <DynamicView :videoId="videoId" :videoData="videoData"></DynamicView>
              </div>
          </div>
      </div>          
  </div>
</template>

<script>
/* global d3 */
// import dataService from './service/dataService.js'
import pipeService from './service/pipeService.js'

import VideoView from './components/VideoView/VideoView.vue'
import MatrixView from './components/MatrixView/MatrixView.vue'
import DynamicView from './components/DynamicView/DynamicView.vue'
import RelationView from './components/RelationView/RelationView.vue'

export default {
  name: 'app',
  components: {
    VideoView,
    MatrixView,
    DynamicView,
    RelationView
  },
  data() {
    return {
        videoList: [],
        videoId: '',
        videoData: {},
        startFrame: 0,
        endFrame: 0,
        clusterNum: 10
    }
  },
  watch: {
  },
  mounted: function () {
    // console.log('d3: ', d3) /* eslint-disable-line */
    // console.log('$: ', $) /* eslint-disable-line */
    // console.log('_', _.partition([1, 2, 3, 4], n => n % 2)) /* eslint-disable-line */

    this.$nextTick(() => {
      pipeService.onSelectVideo((selectedVideo) => {
          this.videoId = selectedVideo
          // dataService.visionData(this.videoId, visionData => {
          //   this.videoData = visionData['data']
          //   // console.log('this.videoData: ', Object.keys(this.videoData));
          // })
          let dataPath = `/data/${selectedVideo}.json`
          console.log('dataPath: ', dataPath)
          d3.json(dataPath).then((visionData) => {
            this.videoData = visionData
          });
      })
    })
  }
}
</script>

