// /* global d3 $ */
import DrawMatrix from './drawMatrix.js'
import pipeService from '../../service/pipeService.js'

export default {
    name: 'MatrixView',
    components: {
    },
    props: {
        videoId: String,
        videoData: Object
    },
    data() {
        return {
            containerId: 'matrixContainer',
            gestureToggled: true
        }
    },
    watch: {
        videoData: function (videoData) {
            this.drawMatrix.layout(videoData)
        },
        gestureToggled: function(gestureToggled) {
            // console.log('gestureToggled: ', gestureToggled)
            this.gestureToggled = gestureToggled
            pipeService.emitTextShowGesture(this.gestureToggled)
        }
    },
    mounted: function () {
        this.drawMatrix = new DrawMatrix(this.containerId)
    }
}
