/* global d3 $ */
// import pipeService from '../../service/pipeService'
import globalConfig from '../../service/globalConfig.js'
import pipeService from '../../service/pipeService.js'

let DrawDynamic= function (id) {	
	this.id = id
    this.svgWidth = $('#' + id).width()
    this.svgHeight = $('#' + id).height()
    this.margin = { top: 0, right: 0, bottom: 0, left: 0 }
    this.sliderHeight = 30
    this.width = this.svgWidth - this.margin.left - this.margin.right
    this.height = this.svgHeight - this.margin.top - this.margin.bottom - this.sliderHeight

    this.svg = d3.select('#' + id).append('svg')
        .attr('class', 'dynamicSvg')
        .attr('width', this.svgWidth)
        .attr('height', this.svgHeight)
        
    this.sliderContainer = this.svg.append('g')
        .attr('class', 'sliderContainer')
        .attr('transform', 'translate(' + (this.margin.left+15) + ', ' + (this.margin.top+15) + ')')
        

	this.graphContainer = this.svg.append('g')
		.attr('class', 'g_main')
        .attr('transform', 'translate(' + this.margin.left + ', ' + (this.margin.top+this.sliderHeight)  + ')')
    
    // console.log('globalConfig: ', globalConfig)

    /////////////////////////////////////////////////////////////////////////////////
    // gestureSpace
    this.gestureSpaceContainer = this.graphContainer.append('g')
        .attr('class', 'gestureSpaceContainer')

}

DrawDynamic.prototype.layout = function (data) {
    this.visionData = data
    this.videoInfo = this.visionData['videoInfo']
    this.poseData = this.visionData['poseData']

    this.bisect = d3.bisector(function(d) { return d }).left
    let sampleInterval = 1
    this.frameIdxList = d3.range(0, this.videoInfo['totalFrmNum'], sampleInterval)
    this.selectedFrameIdx = 0
    this.sampleRatio = 1/3
    this.fps = Math.ceil(this.videoInfo['fps'])

    let formatTime = d3.timeFormat('%M:%S')
    this.formatMinutes = function(d) { return formatTime(new Date(2017, 0, 1, 0, 0, d)) }

    this.timer = null

    this.frameSelection = []

    // this.dynamicTimer = setInterval(function(){ console.log('dynamicTimer') }, 1000)
    // ///////////////////////////////////////////////////////////////////////////////
    // gesture space
    this.drawGestureSpace()

    this.drawSkeleton(0)

    pipeService.onAnimationFrameFromVideo((curFrame) => {
        if (this.frameSelection.length != 0 && this.frameSelection[0] <= curFrame && curFrame <= this.frameSelection[1]) {
            this.handle.attr('cx', this.sliderXScale(curFrame))
            this.label.attr('x', this.sliderXScale(curFrame)).text(this.formatMinutes(curFrame / this.fps)) 
            this.drawSkeleton(curFrame)
            this.highlightMovement(curFrame)
        }
    })

    this.drawMovementAnimation([0, 0])

    pipeService.onFrameBrush((frameSelection) => {
        this.frameSelection = frameSelection
        // console.log('onFrameBrush, frameSelection: ', frameSelection)
        this.drawMovement(frameSelection)
        this.drawMovementAnimation(frameSelection)
    })

}

DrawDynamic.prototype.drawGestureSpace = function () {
    this.gestureSpaceContainer.selectAll('*').remove()
    let containerWidth = this.width
    let containerHeight = this.height

    // console.log('this.width: ', this.width)
    // console.log('this.height: ', this.height)

    let containerRatio = containerWidth/containerHeight
    let initGestureWidth = globalConfig.gestureSpace.extremePeriphery.width
    let initGestureHeight = globalConfig.gestureSpace.extremePeriphery.height
    let gestureSpaceRatio = initGestureWidth/initGestureHeight

    if (gestureSpaceRatio>containerRatio) {
        containerHeight = containerWidth/gestureSpaceRatio
    } else {
        containerWidth = containerHeight*gestureSpaceRatio
    }

    let xOffset = (this.width - containerWidth) / 2
    let yOffset = (this.height - containerHeight) / 2

    let offsetMargin = { top: yOffset, right: xOffset, bottom: yOffset, left: xOffset }

    let shiftDist = 25

    let spaceContainer = this.gestureSpaceContainer.append('g')
        .attr('class', 'spaceContainer')
        .attr('transform', 'translate(' + offsetMargin.left + ', ' + (offsetMargin.top + shiftDist) + ')')

    this.skeletonContainer = this.gestureSpaceContainer.append('g')
        .attr('class', 'skeletonContainer')
        .attr('transform', 'translate(' + offsetMargin.left + ', ' + (offsetMargin.top + shiftDist) + ')')

    this.movementContainer = this.gestureSpaceContainer.append('g')
        .attr('class', 'movementContainer')
        .attr('transform', 'translate(' + offsetMargin.left + ', ' + (offsetMargin.top + shiftDist) + ')')
    
    let xScale = d3.scaleLinear()
        .domain([globalConfig.gestureSpace.extremePeriphery.left, globalConfig.gestureSpace.extremePeriphery.left+globalConfig.gestureSpace.extremePeriphery.width])
        .range([0, containerWidth])
    this.gestureSpaceXScale = xScale

    let yScale = d3.scaleLinear()
        .domain([globalConfig.gestureSpace.extremePeriphery.top, globalConfig.gestureSpace.extremePeriphery.top+globalConfig.gestureSpace.extremePeriphery.height])
        .range([0, containerHeight])
    this.gestureSpaceYScale = yScale

    //////////////////////////////////////////////////////////////
    // gesture space
    // let gestureArea = ['centerCenter', 'center', 'periphery', 'extremePeriphery']
    let gestureArea = ['centerCenter', 'center', 'periphery']
    spaceContainer
        .selectAll('rect')
        .data(gestureArea)
        .enter()
        .append('rect')
        .attr('x', (d) => xScale(globalConfig.gestureSpace[d].left))
        .attr('y', (d) => yScale(globalConfig.gestureSpace[d].top))
        .attr('width', (d) => {
            return xScale(globalConfig.gestureSpace[d].left+globalConfig.gestureSpace[d].width) - xScale(globalConfig.gestureSpace[d].left)
        })
        .attr('height', (d) => {
            return yScale(globalConfig.gestureSpace[d].top+globalConfig.gestureSpace[d].height) - yScale(globalConfig.gestureSpace[d].top)
        })
        .style('fill', 'rgba(0,0,0,0)')
        .style('stroke', '#2378ae')
        .style('stroke-dasharray', '2,2')
        .style('stroke-linecap', 'butt')
        .style('stroke-width', '1')
        .style('opacity', 0.3)
}

DrawDynamic.prototype.drawMovement = function (frameSelection) {
    this.movementContainer.selectAll('*').remove()
    let xScale = this.gestureSpaceXScale
    let yScale = this.gestureSpaceYScale

    let movementData = this.getMovementData(frameSelection)
    let selectedPoseDataList = movementData['selectedPoseDataList']

    
    let oneMovementContainer = this.movementContainer

    let leftHandPoints = []
    let rightHandPoints = []
    for (let i=0; i<selectedPoseDataList.length; i++) {
        if (selectedPoseDataList[i]['leftHand'].length>0) {
            leftHandPoints.push(selectedPoseDataList[i]['leftHand'])
        }
        if (selectedPoseDataList[i]['rightHand'].length>0) {
            rightHandPoints.push(selectedPoseDataList[i]['rightHand'])
        }
    }

    this.leftHandPoints = leftHandPoints
    this.rightHandPoints = rightHandPoints

    let line = d3.line()
        .x((d) => xScale(d[0]))
        .y((d) => yScale(d[1])) 
        .curve(d3.curveMonotoneX)

    oneMovementContainer.append('g')
        .attr('class', 'leftHandLine')
        .append('path')
        .datum(leftHandPoints)
        .attr('class', 'handLine')
        .attr('d', line)

    oneMovementContainer.append('g')
        .attr('class', 'rightHandLine')
        .append('path')
        .datum(rightHandPoints)
        .attr('class', 'handLine')
        .attr('d', line)

    // draw movement circle
    oneMovementContainer.append('g')
        .attr('class', 'leftHandCircle')
        .selectAll('cirlce')
        .data(leftHandPoints)
        .enter()
        .append('circle')
        .attr('class', 'leftCircle')
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => yScale(d[1]))
        .attr('r', function (_, i) {
            if (i==0 || i==leftHandPoints.length-1) {
                return 5
            } else {
                return 1
            }
        })
        .style('fill', function () {
            return globalConfig.leftHandColor
            // if (i==0) {
            //     return '#80b1d3'
            // } else if (i==leftHandPoints.length-1) {
            //     return '#b3de69'
            // }
            // else {
            //     return '#fc8d62'
            // }
        })
        .style('position', 'relative')
        .style('z-index', function (_, i) {
            if (i==0 || i==leftHandPoints.length-1) {
                return 100
            } else {
                return 0
            }
        })
        .style('opacity', 0.1)

    oneMovementContainer.append('g')
        .attr('class', 'rightHandCircle')
        .selectAll('cirlce')
        .data(rightHandPoints)
        .enter()
        .append('circle')
        .attr('class', 'rightHand')
        .attr('cx', d => xScale(d[0]))
        .attr('cy', d => yScale(d[1]))
        .attr('r', function (_, i) {
            if (i==0 || i==rightHandPoints.length-1) {
                return 5
            } else {
                return 1
            }
        })
        .style('fill', function () {
            return globalConfig.rightHandColor
            // if (i==0) {
            //     return '#80b1d3'
            // } else if (i==rightHandPoints.length-1) {
            //     return '#b3de69'
            // } else {
            //     return '#bc80bd'
            // }
        })
        .style('position', 'relative')
        .style('z-index', function (_, i) {
            if (i==0 || i==rightHandPoints.length-1) {
                return 100
            } else {
                return 0
            }
        })
        .style('opacity', 0.1)
}

DrawDynamic.prototype.highlightMovement = function (curFrame) {
    const _this = this
    this.movementContainer.select('g.highlightHands').remove()

    let highlightContainer = this.movementContainer.append('g').attr('class', 'highlightHands')
    
    let pointIdx = curFrame - this.frameSelection[0]
    
    let curLeftHandPoints = this.leftHandPoints.slice(0, pointIdx+1)
    let curRightHandPoints = this.rightHandPoints.slice(0, pointIdx+1)

    // draw movement circle
    highlightContainer.append('g')
        .attr('class', 'leftHandCircle')
        .selectAll('cirlce')
        .data(curLeftHandPoints)
        .enter()
        .append('circle')
        .attr('class', 'leftCircle')
        .attr('cx', d => _this.gestureSpaceXScale(d[0]))
        .attr('cy', d => _this.gestureSpaceYScale(d[1]))
        .attr('r', function (_, i) {
            if (i==0 || i==_this.leftHandPoints.length-1 || i==pointIdx) {
                return 5
            } else {
                return 1
            }
        })
        .style('fill', function () {
            return globalConfig.leftHandColor
            // if (i==0) {
            //     return '#b3de69'
            // } else if (i==_this.leftHandPoints.length-1) {
            //     return '#80b1d3'
            // }
            // else {
            //     return '#fc8d62'
            // }
        })
        .style('position', 'relative')
        .style('z-index', function (_, i) {
            if (i==0 || i==_this.leftHandPoints.length-1) {
                return 100
            } else {
                return 0
            }
        })
        .style('opacity', function (_, i) {
            if (pointIdx-i<10) {
                return 1
            } else {
                return 0
            }
        })

    highlightContainer.append('g')
        .attr('class', 'rightHandCircle')
        .selectAll('cirlce')
        .data(curRightHandPoints)
        .enter()
        .append('circle')
        .attr('class', 'rightHand')
        .attr('cx', d => _this.gestureSpaceXScale(d[0]))
        .attr('cy', d => _this.gestureSpaceYScale(d[1]))
        .attr('r', function (_, i) {
            if (i==0 || i==_this.rightHandPoints.length-1 || i===pointIdx) {
                return 5
            } else {
                return 1
            }
        })
        .style('fill', function () {
            return globalConfig.rightHandColor
            // if (i==0) {
            //     return '#b3de69'
            // } else if (i==_this.rightHandPoints.length-1) {
            //     return '#80b1d3'
            // } else {
            //     return '#bc80bd'
            // }
        })
        .style('position', 'relative')
        .style('z-index', function (_, i) {
            if (i==0 || i==_this.rightHandPoints.length-1) {
                return 100
            } else {
                return 0
            }
        })
        .style('opacity', function (_, i) {
            if (pointIdx-i<100) {
                return 1
            } else {
                return 0
            }
        })

}

DrawDynamic.prototype.drawMovementAnimation = function (frameSelection) {
    this.sliderContainer.selectAll('*').remove()
    this.skeletonContainer.selectAll('*').remove()

    const _this = this

    /////  slider
    // button
    let buttonWidth = 40
    let buttonHeight = 20

    let rectButton = this.sliderContainer
        .append('g')
        .attr('transform', 'translate(0, ' + (this.sliderHeight/2-buttonHeight/2) + ')')
        .append('rect')
        .attr('class', 'play-button')
        .attr('rx', 5)
        .attr('ry', 5)
        .attr('width', buttonWidth)
        .attr('height', buttonHeight)
    
    let rectLabel = this.sliderContainer
        .append('g')
        .attr('transform', 'translate(' + (buttonWidth/2) + ',' + (this.sliderHeight/2+5) + ')')
        .append('text')
        .attr('class', 'rectLabel')
        .attr('text-anchor', 'middle')
    
    rectLabel.text('Play')

    rectButton.on('click', function() {
        if (rectLabel.text() == 'Pause') {
            moving = false
            rectLabel.text('Play')
            pipeService.emitPlayVideoFromSlider({
                'status': 'pause',
                'frameSelection': frameSelection
            })
        } else {
            moving = true;
            rectLabel.text('Pause')
            pipeService.emitPlayVideoFromSlider({
                'status': 'play',
                'frameSelection': frameSelection
            })
        }
        console.log('Slider moving: ' + moving)
    })

    
    let moving = false
    let buttonGap = 20
    let sliderWidth = this.width-buttonWidth-buttonGap-50
    // slider 
    let x = d3.scaleLinear()
        .domain([frameSelection[0], frameSelection[1]])
        .range([0, sliderWidth]) 
        .clamp(true)

    this.sliderXScale = x
    // let currentValue = x(frameSelection[0])
    // let targetValue = sliderWidth

    let slider = this.sliderContainer
        .append('g')
        .attr('class', 'slider')
        .attr('transform', 'translate(' + (buttonWidth+buttonGap) + ',' + (this.sliderHeight/2) + ')')

    slider.append('line')
        .attr('class', 'track')
        .attr('x1', 0)
        .attr('x2', sliderWidth)
        .select(function() { return this.parentNode.appendChild(this.cloneNode(true)) })
        .attr('class', 'track-inset')
        .select(function() { return this.parentNode.appendChild(this.cloneNode(true)) })
        .attr('class', 'track-overlay')
        .call(d3.drag()
            .on('start.interrupt', function() { slider.interrupt() })
            .on('start drag', function() {
                let currentValue = d3.event.x
                update(x.invert(currentValue))
            })
        )

    slider.insert('g', '.track-overlay')
            .attr('class', 'ticks')
            .attr('transform', 'translate(0,' + 18 + ')')
            .selectAll('text')
            // .data(x.ticks(2))
            .data(x.domain())
            .enter()
            .append('text')
            .attr('x', x)
            .attr('y', 10)
            .attr('text-anchor', 'middle')
            // .text(function(d) { return d })
            .text(function(d) { return _this.formatMinutes(d / _this.fps) })
            .style('font-size', '12px')

            

    this.handle = slider.insert('circle', '.track-overlay')
            .attr('class', 'sliderHandle')
            .attr('r', 5)

    this.label = slider.append('text')
            .attr('class', 'label')
            .attr('text-anchor', 'middle')
            .text(_this.formatMinutes(frameSelection[0] / _this.fps))
            .style('font-size', '12px')
            .attr('transform', 'translate(0,' + (-10) + ')')

    // console.log('handle: ', handle)
    // console.log('label: ', label)
    // 

    // function step() {
    //     update(x.invert(currentValue));
    //     currentValue = currentValue + (targetValue/100)
    //     // currentValue = currentValue + 1
    //     if (currentValue > targetValue) {
    //         moving = false;
    //         currentValue = 0;
    //         clearInterval(_this.timer);
    //         // timer = 0;
    //         rectButton.text('Play')
    //         // console.log('Slider moving: ' + moving)
    //     }
    // }

    // function update(h) {
    //     // update position and text of label according to slider scale
    //     handle.attr('cx', x(h))
    //     label.attr('x', x(h)).text(Math.round(h))
    //     // console.log('h: ', h)
    //     let currentFrame = Math.round(h)

    //     _this.drawSkeleton(currentFrame)

    //     pipeService.emitFrameSelectedFromAnimation(currentFrame)
    //     // if (currentFrame%30 == 0) {
    //     //     pipeService.emitFrameSelectedFromAnimation(currentFrame)
    //     // }
    // }

    function update(h) {
        // update position and text of label according to slider scale
        _this.handle.attr('cx', x(h))
        _this.label.attr('x', x(h)).text(_this.formatMinutes(h / _this.fps))
        let currentFrame = Math.round(h)
        _this.drawSkeleton(currentFrame)
        pipeService.emitVideoFrameFromSlider(currentFrame)
    }

}

DrawDynamic.prototype.drawSkeleton = function (currentFrame) {
//////////////////////////////////////////////////////////////
    // console.log('currentFrame: ', currentFrame)

    if (this.poseData[currentFrame.toString()]['people'].length !== 1) return

    let pose_keypoints = this.poseData[currentFrame.toString()]['people'][0]['pose_keypoints_2d']
    if (pose_keypoints[0*3+2]===0 || pose_keypoints[1*3+2]===0 || pose_keypoints[2*3+2]===0 || pose_keypoints[5*3+2]===0 || pose_keypoints[8*3+2]===0 ) return
    
    let jointScale = d3.scaleLinear() // joint 2 and 5; distance scale
        .domain([0, Math.abs(pose_keypoints[1*3+1]-pose_keypoints[8*3+1])])
        .range([0, Math.abs(globalConfig.skeletonInit.points['1'][1]-globalConfig.skeletonInit.points['8'][1])])

    this.skeletonContainer.selectAll('*').remove()

    // skeleton
    let bodyPoints = []
    let bodyPointNum = 25
    for (let i=0; i<bodyPointNum; i++) {
        if (pose_keypoints[i*3+2]===0) {
            bodyPoints.push([])
        } else {
            let currentPoint = [
                globalConfig.skeletonInit.points['1'][0]+jointScale(pose_keypoints[i*3+0]-pose_keypoints[1*3+0]), 
                globalConfig.skeletonInit.points['1'][1]+jointScale(pose_keypoints[i*3+1]-pose_keypoints[1*3+1])
            ]
            bodyPoints.push([
                this.gestureSpaceXScale(currentPoint[0]), 
                this.gestureSpaceYScale(currentPoint[1])
            ])
        }
    }

    let bodyPointPairData = globalConfig.BODY25Pairs

    this.skeletonContainer.append('g')
        .attr('class', 'body-circle-connection')
        .selectAll('line')
        .data(bodyPointPairData.filter(d => {
            if (bodyPoints[d[0]].length !=0 && bodyPoints[d[1]].length != 0) {
                return true
            } else {
                return false
            }
        }))
        .enter()
        .append('line')
        .attr('x1', d => bodyPoints[d[0]][0])
        .attr('y1', d => bodyPoints[d[0]][1])
        .attr('x2', d => bodyPoints[d[1]][0])
        .attr('y2', d => bodyPoints[d[1]][1])
        .style('stroke', '#bdbdbd')
        .style('stroke-width', 2) // original one is 4
        .style('opacity', 0.3)

    // skeleton points
    this.skeletonContainer.append('g')
        .attr('class', 'skeletonCircles')
        .selectAll('cirlce')
        .data(bodyPoints.filter((d) => {
            if (d.length != 0) {
                return true
            } else {
                return false
            }
        }))
        .enter()
        .append('circle')
        .attr('cx', d => d[0])
        .attr('cy', d => d[1])
        .attr('r', 2)
        .style('fill', 'red')
        .style('opacity', 0.3)
}

DrawDynamic.prototype.getMovementData = function (frameSelection) {
    // only select those frames contain joints 0, 1, 2, 5, 8

    let jointXScale = d3.scaleLinear() // joint 2 and 5; distance scale
        .range([0, Math.abs(globalConfig.skeletonInit.points['1'][1]-globalConfig.skeletonInit.points['8'][1])])

    let jointYScale = d3.scaleLinear() // joint 1 and 8; ; distance scale
        .range([0, Math.abs(globalConfig.skeletonInit.points['1'][1]-globalConfig.skeletonInit.points['8'][1])])

    let selectedPoseDataList = []
    let selectedFrameIdxList = []
    for (let i=frameSelection[0]; i<frameSelection[1]; i++) {
        if (this.poseData[i.toString()]['people'].length !== 1) continue
        let pose_keypoints = this.poseData[i.toString()]['people'][0]['pose_keypoints_2d']
        if (pose_keypoints[0*3+2]===0 || pose_keypoints[1*3+2]===0 || pose_keypoints[2*3+2]===0 || pose_keypoints[5*3+2]===0 || pose_keypoints[8*3+2]===0 ) continue
        if (pose_keypoints[4*3+2]===0 && pose_keypoints[7*3+2]===0) continue
        // track joint position: right hand 4; left hand 7;
        // track angle: right 2-3-4; left 5, 6, 7

        // jointXScale.domain([0, Math.abs(pose_keypoints[2*3+0]-pose_keypoints[5*3+0])])
        jointXScale.domain([0, Math.abs(pose_keypoints[1*3+1]-pose_keypoints[8*3+1])])
        jointYScale.domain([0, Math.abs(pose_keypoints[1*3+1]-pose_keypoints[8*3+1])])

        let rightHand = []
        let leftHand = []
        if (pose_keypoints[4*3+2]!=0) {
            rightHand = [
                globalConfig.skeletonInit.points['1'][0]+jointXScale(pose_keypoints[4*3+0]-pose_keypoints[1*3+0]),
                globalConfig.skeletonInit.points['1'][1]+jointYScale(pose_keypoints[4*3+1]-pose_keypoints[1*3+1])
            ]
        }
        if (pose_keypoints[7*3+2]!=0) {
            leftHand = [
                globalConfig.skeletonInit.points['1'][0]+jointXScale(pose_keypoints[7*3+0]-pose_keypoints[1*3+0]),
                globalConfig.skeletonInit.points['1'][1]+jointYScale(pose_keypoints[7*3+1]-pose_keypoints[1*3+1])
            ]
        }
        selectedPoseDataList.push({'leftHand': leftHand, 'rightHand': rightHand})
        selectedFrameIdxList.push(i)
    }
    // joint points are map to standard gesture space
    return {
        'selectedPoseDataList': selectedPoseDataList,
        'selectedFrameIdxList': selectedFrameIdxList,
    }
}

export default DrawDynamic