DrawRelation.prototype.layout = function (data) {
    // console.log('relation videoData: ', data)
    this.visionData = data
    this.videoInfo = this.visionData['videoInfo']
    this.fps = Math.ceil(this.videoInfo['fps'])
    this.totalFrameNum = this.videoInfo['totalFrmNum']
    this.poseData = this.visionData['poseData']
    this.gestureSpaceData = this.visionData['gestureSpaceData']
    // process word phrase
    let wordPhrase = this.visionData['wordPhrase']
    // console.log('wordPhrase: ', wordPhrase)

    let getWordPhraseDataResult = this.visionData['wordPhraseDataResult']
    let positionInfo = {}
    let wordPhraseData = {}


    if(Object.keys(getWordPhraseDataResult).length === 0) {
        // console.log('relation getWordPhraseDataResult: recalculation')
        getWordPhraseDataResult = this.getWordPhraseData(wordPhrase)
        this.numMax = getWordPhraseDataResult['numMax']
        wordPhraseData = getWordPhraseDataResult['wordPhraseData']
        // console.log('wordPhraseData: ', wordPhraseData)
        positionInfo = this.calculatePosition(wordPhraseData)
        getWordPhraseDataResult['positionInfo'] = positionInfo
        dataService.saveData(this.videoInfo['videoId'], 'wordPhraseDataResult', getWordPhraseDataResult, (feedback) => {
                console.log('saveWordPhraseDataResult, feedback: ', feedback)
        })
    } else {
        // console.log('relation getWordPhraseDataResult: load from backend')
        wordPhraseData = getWordPhraseDataResult['wordPhraseData']
        positionInfo = getWordPhraseDataResult['positionInfo']
        this.numMax = getWordPhraseDataResult['numMax']
    }
    // load from backend

    ////////////////////////////////////////////////////////////////
    
    // textPosition
    this.textXScale = d3.scaleLinear()
        .domain(positionInfo['textPositionInfo']['xDomain'])
        .range([0, this.upperWidth]) 
    this.textYScale = d3.scaleLinear()
        .domain(positionInfo['textPositionInfo']['yDomain'])
        .range([this.upperHeight, 0])
    this.textRScale = d3.scaleLinear()
            .domain(positionInfo['textPositionInfo']['rDomain'])
            .range([4, 15])   
    // zoom
    this.textXScaleZoom = this.textXScale
    this.textYScaleZoom = this.textYScale
    this.textNewZoomXScale = this.textXScale
    this.textNewZoomYScale = this.textYScale

    // gesturePosition
    this.gestureXScale = d3.scaleLinear()
        .domain(positionInfo['gesturePositionInfo']['xDomain'])
        .range([0, this.lowerWidth]) 
    this.gestureYScale = d3.scaleLinear()
        .domain(positionInfo['gesturePositionInfo']['yDomain'])
        .range([this.lowerHeight, 0])
    this.gestureSpaceScale = d3.scaleLinear()
        .domain([0, 1])
        .range([0, 2*16])
    // zoom
    this.gestureXScaleZoom = this.gestureXScale
    this.gestureYScaleZoom = this.gestureYScale
    this.gestureNewZoomXScale = this.gestureXScale
    this.gestureNewZoomYScale = this.gestureYScale


    this.distanceChangeScale = d3.scaleLinear()
        .domain(positionInfo['distanceChangeInfo']['distanceChangeDomain'])
        .range([0, 8])


    ////////////////////////////////////////////////////////////////

    this.drawCluster(wordPhraseData)


    pipeService.onTimeFilter((timeFilter) => {
        // console.log('timeFilter: ', timeFilter)
        this.timeFilter = timeFilter
        this.drawCluster(wordPhraseData)
    })

    pipeService.onNumFilter((numFilter) => {
        // console.log('numFilter: ', numFilter)
        this.numFilter = numFilter
        this.drawCluster(wordPhraseData)
    })
    pipeService.onLegendFilter(() => {
        this.drawCluster(wordPhraseData)
    })

    pipeService.onHighlightPoint((frameRangeSelection) => {
        // console.log('frameRangeSelection: ', frameRangeSelection)

        if (frameRangeSelection.length == 0) {
            d3.selectAll('.text-circles')
                .style('stroke-width', '0px')

            d3.selectAll('.gesture-circles')
                .style('stroke-opacity', 0.1)
                .style('stroke', 'black')

            d3.selectAll('.circle-texts')
                .style('fill', 'black')
                .style('font-weight', 300)
        } else {

            d3.selectAll('.text-circles')
                .style('stroke-width', '0px')

            d3.selectAll('.gesture-circles')
                .style('stroke-opacity', 0.1)
                .style('stroke', 'black')

            let startTime = frameRangeSelection[0]/_this.fps
            let endTime = frameRangeSelection[1]/_this.fps

            d3.selectAll('.text-circles')
                .filter((d) => {
                    let timeRangeList = d['timeRange']
                    for (let i=0; i<timeRangeList.length; i++) {
                        if (timeRangeList[i][0]>=startTime && timeRangeList[i][1]<=endTime) return true
                    }
                    return false
                })
                .style('stroke', 'red')
                .style('stroke-opacity', 1)
                .style('stroke-width', '1px')

            d3.selectAll('.circle-texts')
                .filter((d) => {
                    let timeRangeList = d['timeRange']
                    for (let i=0; i<timeRangeList.length; i++) {
                        if (timeRangeList[i][0]>=startTime && timeRangeList[i][1]<=endTime) return true
                    }
                    return false
                })
                .style('fill', 'black')
                .style('font-weight', 350)


            d3.selectAll('.gesture-circles')
                .filter((d) => {
                    let timeRange = d['timeRange']
                    if (timeRange[0]>=startTime && timeRange[1]<=endTime) {
                        return true
                    } else {
                        return false
                    }
                })
                .style('stroke', 'red')
                .style('stroke-opacity', 1)
                .style('stroke-width', '1px')
        }
    })

    const _this = this
    $('#lassoButton').on('click', function(){
        if (!_this.lassoFlag) {
            d3.select(this).style('color', 'red')

            _this.upperContainer.style('pointer-events', 'none')
            _this.upperContainer.select('.zoomArea').style('pointer-events', 'none')
            _this.lowerContainer.style('pointer-events', 'none')
            _this.lowerContainer.select('.zoomArea').style('pointer-events', 'none')
            
        } else {
            d3.select(this).style('color', 'black')
            
            _this.upperContainer.style('pointer-events', 'auto')
            _this.upperContainer.select('.zoomArea').style('pointer-events', 'auto')
            _this.lowerContainer.style('pointer-events', 'auto')
            _this.lowerContainer.select('.zoomArea').style('pointer-events', 'auto')
        }
        _this.lassoFlag = !_this.lassoFlag
    })

    $('#refreshButton').on('click', function(){
        _this.upperZoomedTransform = {}
        _this.linkPointData = []

        _this.textRefreshFlag = true
        _this.gestureRefreshFlag = true

        _this.textXScaleZoom = _this.textXScale
        _this.textYScaleZoom = _this.textYScale
        _this.textNewZoomXScale = this.textXScale
        _this.textNewZoomYScale = this.textYScale
        _this.gestureXScaleZoom = _this.gestureXScale
        _this.gestureYScaleZoom = _this.gestureYScale
        this.gestureNewZoomXScale = this.gestureXScale
        this.gestureNewZoomYScale = this.gestureYScale


        if (_this.textRefreshFlag) {
            _this.textRefreshFlag = false
            _this.upperContainer.call(_this.upperZoom.transform, d3.zoomIdentity)
        }

        if (_this.gestureRefreshFlag) {
            _this.gestureRefreshFlag = false
            _this.lowerContainer.call(_this.lowerZoom.transform, d3.zoomIdentity)
        }

        _this.drawCluster(wordPhraseData)
        d3.selectAll('.linkPaths').remove()
    })
}


