DrawRelation.prototype.getWordPhraseData = function (wordPhrase) {
    let wordPhraseData = []
    let maxTimeRangeNum = 0
    let phraseIdx = 0

    for (let phraseType in wordPhrase) {
        let phraseTypeItem = wordPhrase[phraseType]
        let phraseTmp = {}
        if (phraseType == 'entities') continue

        if (phraseType != 'subject_verb_object') {
            for (let i=0; i<phraseTypeItem.length; i++) {
                let phraseText = phraseTypeItem[i]['text'].toLowerCase()
                // if (phraseTypeItem[i]['start_time'] == undefined || phraseTypeItem[i]['end_time'] == undefined) {
                //     continue
                // }
                if (phraseText in phraseTmp) {
                    phraseTmp[phraseText]['timeRange'].push([parseFloat(phraseTypeItem[i]['start_time']), parseFloat(phraseTypeItem[i]['end_time'])])
                } else {
                    phraseTmp[phraseText] = {
                        'timeRange': [[parseFloat(phraseTypeItem[i]['start_time']), parseFloat(phraseTypeItem[i]['end_time'])]],
                        'vector': phraseTypeItem[i]['vector']
                    }
                }
            }
        }
        else {
            for (let i=0; i<phraseTypeItem.length; i++) {
                let phraseText = `${phraseTypeItem[i][0]['text']} ${phraseTypeItem[i][1]['text']} ${phraseTypeItem[i][2]['text']}`.toLowerCase()
                // if (phraseTypeItem[i][0]['start_time'] == undefined || phraseTypeItem[i][0]['end_time'] == undefined) {
                //     continue
                // }
                let timeRange = [parseFloat(phraseTypeItem[i][0]['start_time']), parseFloat(phraseTypeItem[i][0]['end_time'])]
                let vector = []
                for (let j=0; j<phraseTypeItem[i][0]['vector'].length; j++) {
                    vector.push((phraseTypeItem[i][0]['vector'][j]+phraseTypeItem[i][1]['vector'][j]+phraseTypeItem[i][2]['vector'][j])/3)
                }

                if (phraseText in phraseTmp) {
                    phraseTmp[phraseText]['timeRange'].push(timeRange)
                } else {
                    phraseTmp[phraseText] = {
                        'timeRange': [timeRange],
                        'vector': vector
                    }
                }
            }
        }
        
        for (let wordText in phraseTmp) {
            let phraseTmpItem = phraseTmp[wordText]

            // console.log('phraseIdx: ', phraseIdx)
            // console.log('wordText: ', wordText)
            
            let curTimeRange = []
            for (let i=0; i<phraseTmpItem['timeRange'].length; i++) {
                if (phraseTmpItem['timeRange'][i][0] == undefined || phraseTmpItem['timeRange'][i][1] == undefined 
                || isNaN(phraseTmpItem['timeRange'][i][0]) || isNaN(phraseTmpItem['timeRange'][i][1])) {
                    continue
                }
                curTimeRange.push(phraseTmpItem['timeRange'][i])
            }
            if (curTimeRange.length == 0) {
                continue
            }

            // console.log('curTimeRange: ', curTimeRange)

            wordPhraseData.push({
                'phraseIdx': phraseIdx,
                'vector': phraseTmpItem['vector'],
                'timeRange': curTimeRange,
                'type': phraseType,
                'text': wordText
            })
            phraseIdx += 1
            if (curTimeRange.length > maxTimeRangeNum) maxTimeRangeNum = curTimeRange.length

            // wordPhraseData.push({
            //     'phraseIdx': phraseIdx,
            //     'vector': phraseTmpItem['vector'],
            //     'timeRange': phraseTmpItem['timeRange'],
            //     'type': phraseType,
            //     'text': wordText
            // })
            // phraseIdx += 1
            // if (phraseTmpItem['timeRange'].length > maxTimeRangeNum) maxTimeRangeNum = phraseTmpItem['timeRange'].length
        }
    }
    // this.numMax = maxTimeRangeNum
    return {
        'wordPhraseData': wordPhraseData,
        'numMax': maxTimeRangeNum
    }
}

DrawRelation.prototype.calculatePosition = function (wordPhraseData) {
    // calculate text position - T-SNE
    let textPositionInfo = this.calculateTextPosition(wordPhraseData)

    // calculate gesture position - T-SNE
    let distanceChangeInfo = this.calculateAverageJoint(wordPhraseData)
    let gesturePositionInfo = this.calculateGesturePosition(wordPhraseData)

    let positionInfo = {
        'textPositionInfo': textPositionInfo,
        'gesturePositionInfo': gesturePositionInfo,
        'distanceChangeInfo': distanceChangeInfo
    }
    return positionInfo
}

DrawRelation.prototype.calculateTextPosition = function (wordPhraseData) {

    let opt = {}
    opt.epsilon = 10; // epsilon is learning rate (10 = default)
    opt.perplexity = 30; // roughly how many neighbors each point influences (30 = default)
    opt.dim = 2; // dimensionality of the embedding (2 = default)

    let tsne = new tsnejs.tSNE(opt); // create a tSNE instance

    // initialize data. Here we have 3 points and some example pairwise dissimilarities
    let X = []
    for (let i=0; i<wordPhraseData.length; i++) {
        X.push(wordPhraseData[i]['vector'])
    }

    tsne.initDataRaw(X)

    for(let k = 0; k < 500; k++) {
        tsne.step(); // every time you call this, solution gets better
    }

    let Y = tsne.getSolution(); // Y is an array of 2-D points that you can plot

    for (let i=0; i<wordPhraseData.length; i++) {
        wordPhraseData[i]['textPosition'] = Y[i]
    }

    // get textPosition domain
    let xMax = -Infinity
    let xMin = Infinity
    let yMax = -Infinity
    let yMin = Infinity
    let rMax = -Infinity
    let rMin = Infinity

    for (let i=0; i<wordPhraseData.length; i++) {
        let position = wordPhraseData[i]['textPosition']
        if (position[0] > xMax) xMax = position[0]
        if (position[0] < xMin) xMin = position[0]
        if (position[1] > yMax) yMax = position[1]
        if (position[1] < yMin) yMin = position[1]

        let timeRangeNum = wordPhraseData[i]['timeRange'].length
        if (timeRangeNum > rMax) rMax = timeRangeNum
        if (timeRangeNum < rMin) rMin = timeRangeNum
    }

    let positionInfo = {
        'xDomain': [xMin, xMax],
        'yDomain': [yMin, yMax],
        'rDomain': [rMin, rMax]
    }

    // this.textXScale = d3.scaleLinear()
    //     .domain(positionInfo['xDomain'])
    //     .range([0, this.upperWidth]) 

    // this.textYScale = d3.scaleLinear()
    //     .domain(positionInfo['yDomain'])
    //     .range([this.upperHeight, 0])

    // let radius = 15
    // // this.textRScale = d3.scaleLog()
    // this.textRScale = d3.scaleLinear()
    //         .domain([rMin, rMax])
    //         .range([4, radius])   

    // // zoom
    // this.textXScaleZoom = this.textXScale
    // this.textYScaleZoom = this.textYScale

    return positionInfo
}

DrawRelation.prototype.calculateAverageJoint = function (wordPhraseData) {
    // wordPhraseData
    // this.gestureSpaceData     // this.textData
    let maxChange = -Infinity
    let minChange = Infinity

    for (let i=0; i<wordPhraseData.length; i++) {
        // console.log('wordPhraseData, i: ', i)
        // console.log('wordPhraseData[i], ', wordPhraseData[i])
        let phraseItem = wordPhraseData[i]

        let timeRange = phraseItem['timeRange']
        
        let phraseJointData = []
        let timeRangeJointData = []

        let curTimeRange = []
        for (let j=0; j<timeRange.length; j++) {
            let startTime = timeRange[j][0]
            let endTime = timeRange[j][1]
            let startFrame = Math.max(0, Math.floor(startTime * this.fps))
            let endFrame = Math.min(this.totalFrameNum, Math.ceil(endTime * this.fps))
            let curGesture = this.gestureSpaceData.slice(startFrame, endFrame)
            phraseJointData = phraseJointData.concat(curGesture)
            // timeRangeJointData.push(curGesture.filter((d) => d.length!=0))
            let tmpCurGesture = curGesture.filter((d) => d.length!=0)
            if (tmpCurGesture.length == 0) {
                continue
            } else {
                curTimeRange.push([startTime, endTime])
                timeRangeJointData.push(tmpCurGesture)
            }
        }

        phraseItem['timeRange'] = curTimeRange

        let filterJointData = phraseJointData.filter((d) => d.length!=0)

        // console.log('filterJointData: ', filterJointData)
        // console.log('timeRangeJointData: ', timeRangeJointData)
        phraseItem['jointData'] = filterJointData
        phraseItem['timeRangeJointData'] = timeRangeJointData

        // calculate averageJoint
        if (filterJointData.length === 0) {
            phraseItem['averageJoint'] = []
        } else {
            // calculate average joint
            let averageJoint = []
            for (let k=0; k<8; k++) {
                let jointX = 0
                let jointY = 0
                for (let m=0; m<filterJointData.length; m++) {
                    let jointData = filterJointData[m]
                    jointX += jointData[3*k+0]
                    jointY += jointData[3*k+1]
                }
                let averageJointX = jointX/filterJointData.length
                let averageJointY = jointY/filterJointData.length
                averageJoint.push([averageJointX, averageJointY])
            }
            phraseItem['averageJoint'] = averageJoint
        }

        // calculate timeRangeAverageJoint
        phraseItem['timeRangeAverageJoint'] = []
        phraseItem['timeRangeGestureType'] = []
        for (let j=0; j<timeRangeJointData.length; j++) {
            let curTimeRangeJointData = timeRangeJointData[j]
            let curAverageJoint = []
            for (let k=0; k<8; k++) {
                let jointX = 0
                let jointY = 0
                for (let m=0; m<curTimeRangeJointData.length; m++) {
                    let jointData = curTimeRangeJointData[m]
                    jointX += jointData[3*k+0]
                    jointY += jointData[3*k+1]
                }
                let averageJointX = jointX/curTimeRangeJointData.length
                let averageJointY = jointY/curTimeRangeJointData.length
                curAverageJoint.push([averageJointX, averageJointY])
            }
            phraseItem['timeRangeAverageJoint'].push(curAverageJoint)
            phraseItem['timeRangeGestureType'].push(this.gestureType(curAverageJoint))
        }

        // calucalte variation
    
        phraseItem['gestureDistanceChange'] = []
        for (let j=0; j<timeRangeJointData.length; j++) {
            let curTimeRangeJointData = timeRangeJointData[j]
            let curAverageJoint = phraseItem['timeRangeAverageJoint'][j]
            let curAverageJointData = []
            for (let k=0; k<curAverageJoint.length; k++) {
                curAverageJointData = curAverageJointData.concat([curAverageJoint[k][0], curAverageJoint[k][1], 1])
            }
            // console.log('curAverageJointData: ', curAverageJointData)
            let curDistanceChange = []
            for (let k=0; k<curTimeRangeJointData.length; k++) {
                // let change = this.gestureDistance(curTimeRangeJointData[k], curTimeRangeJointData[k+1])
                let change = this.gestureDistance(curAverageJointData, curTimeRangeJointData[k])
                curDistanceChange.push(change)
                if (change > maxChange) maxChange = change
                if (change < minChange) minChange = change
            }
            phraseItem['gestureDistanceChange'].push(curDistanceChange)
        }
    }


    // this.distanceChangeScale = d3.scaleLinear()
    //     .domain([minChange, maxChange])
    //     .range([0, 8])

    // console.log('this.distanceChangeScale.domain(): ', this.distanceChangeScale.domain())
    return {
        'distanceChangeDomain': [minChange, maxChange]
    }
}

DrawRelation.prototype.calculateGesturePosition = function (wordPhraseData) {
    // console.log('calculateGesturePosition wordPhraseData: ', wordPhraseData)
    let opt = {}
    opt.epsilon = 10; // epsilon is learning rate (10 = default)
    opt.perplexity = 30; // roughly how many neighbors each point influences (30 = default)
    opt.dim = 2; // dimensionality of the embedding (2 = default)

    let tsne = new tsnejs.tSNE(opt); // create a tSNE instance

    // initialize data. Here we have 3 points and some example pairwise dissimilarities
    let mapping_idx_2_phraseIdx = {}
    let mapping_idx = 0

    let X = []
    for (let i=0; i<wordPhraseData.length; i++) {
        wordPhraseData[i]['gesturePositionList'] = []
        let timeRangeAverageJoint = wordPhraseData[i]['timeRangeAverageJoint']
        // console.log('wordPhraseData, i: ', i)
        // console.log('timeRangeAverageJoint: ', timeRangeAverageJoint)
        for (let j=0; j<timeRangeAverageJoint.length; j++) {
            let tmpVector = []
            let curAverageJoint = timeRangeAverageJoint[j]
            for (let k=0; k<curAverageJoint.length; k++) {
                tmpVector = tmpVector.concat(curAverageJoint[k])
            }
            // console.log('wordPhraseData, tmpVector', tmpVector)
            X.push(tmpVector)
            mapping_idx_2_phraseIdx[mapping_idx] = i
            mapping_idx += 1
        }
    }

    // console.log('X: ', X)

    tsne.initDataRaw(X)

    for(let k = 0; k < 500; k++) {
        tsne.step(); // every time you call this, solution gets better
    }

    let Y = tsne.getSolution(); // Y is an array of 2-D points that you can plot

    // console.log('Y: ', Y)

    for (let i=0; i<X.length; i++) {
        let phraseIdx = mapping_idx_2_phraseIdx[i]
        wordPhraseData[phraseIdx]['gesturePositionList'].push(Y[i])
    }

    // gesture position info
    let xMax = -Infinity
    let xMin = Infinity
    let yMax = -Infinity
    let yMin = Infinity
    let rMax = -Infinity
    let rMin = Infinity

    for (let i=0; i<Y.length; i++) {
        let position = Y[i]
        if (position[0] > xMax) xMax = position[0]
        if (position[0] < xMin) xMin = position[0]
        if (position[1] > yMax) yMax = position[1]
        if (position[1] < yMin) yMin = position[1]

        let timeRangeNum = wordPhraseData[mapping_idx_2_phraseIdx[i]]['timeRange'].length
        if (timeRangeNum > rMax) rMax = timeRangeNum
        if (timeRangeNum < rMin) rMin = timeRangeNum
    }

    let positionInfo = {
        'xDomain': [xMin, xMax],
        'yDomain': [yMin, yMax],
        'rDomain': [rMin, rMax]
    }

    // this.gestureXScale = d3.scaleLinear()
    //     .domain(positionInfo['xDomain'])
    //     .range([0, this.lowerWidth]) 

    // this.gestureYScale = d3.scaleLinear()
    //     .domain(positionInfo['yDomain'])
    //     .range([this.lowerHeight, 0])

    // let radius = 16
    // this.gestureSpaceScale = d3.scaleLinear()
    //     .domain([0, 1])
    //     .range([0, 2*radius])

    // // zoom
    // this.gestureXScaleZoom = this.gestureXScale
    // this.gestureYScaleZoom = this.gestureYScale

    return positionInfo
}

DrawRelation.prototype.drawCluster = function (wordPhraseData) {
    let clusterPhraseData = []
    let totalTime = this.totalFrameNum/this.fps
    let timeStart = totalTime*this.timeFilter[0]
    let timeEnd = totalTime*this.timeFilter[1]
    let numStart = this.numFilter[0]
    let numEnd = this.numFilter[1]
    
    // console.log('wordPhraseData: ', wordPhraseData)
    for (let i=0; i<wordPhraseData.length; i++) {
        // console.log('wordPhraseData[i]: ', i, wordPhraseData[i])
        if (wordPhraseData[i]['timeRange'].length == 0)
            continue
        let phraseTimeStart = wordPhraseData[i]['timeRange'][0][0]
        let phraseTimeEnd = wordPhraseData[i]['timeRange'][0][1]
        let phraseNum = wordPhraseData[i]['timeRange'].length
        let phraseType = wordPhraseData[i]['type']

        if (timeStart<= phraseTimeStart && 
            phraseTimeEnd <= timeEnd && 
            numStart<= phraseNum && 
            phraseNum<= numEnd && 
            this.legendFilter.hasOwnProperty(phraseType)) {
                clusterPhraseData.push(wordPhraseData[i])
            } 
    }
    // console.log('clusterPhraseData: ', clusterPhraseData)
    this.drawTextCluster(clusterPhraseData)
    this.drawGestureCluster(clusterPhraseData)

    let lassoData = {
        'circles': this.svg.selectAll('.lasso-circles'),
    }
    d3.selectAll('g.lasso').remove()
    this.drawLasso(lassoData)

}

DrawRelation.prototype.drawTextCluster = function (clusterPhraseData) {
    // draw projection   
    const _this = this 
    // console.log('_this.upperZoomedTransform: ', _this.upperZoomedTransform)
    _this.upperContainer.selectAll('g').remove()

    let pointContainer = _this.upperContainer.append('g')

    let textPosition = clusterPhraseData.map((d) => [_this.textXScaleZoom(d['textPosition'][0]), _this.textYScaleZoom(d['textPosition'][1])])
    // console.log('textPosition: ', textPosition)

    let displayThreshold = 2000
    let voronoi = d3.voronoi().extent([[0, 0], [_this.upperWidth, _this.upperHeight]])

    let cells = voronoi.polygons(textPosition)
    
    // let cellPath = pointContainer.append('g').selectAll('path')
    // cellPath.data(cells).enter().append('path')
    //     .attr('stroke','#ccc')
    //     .style('fill', 'none')
    //     .style('vector-effect', 'non-scaling-stroke')
    //     .attr('d', (d) => 'M' + d.join('L') + 'Z')

    let points = pointContainer.selectAll('g')
        .data(clusterPhraseData)
        .enter()
        .append('g')
        .attr('transform', (d) => 'translate(' + _this.textXScaleZoom(d['textPosition'][0]) + ', ' + _this.textYScaleZoom(d['textPosition'][1]) + ')')
    
    points.append('circle')
        .attr('class', (d) => 'lasso-circles text-circles text-circle-'+d['phraseIdx'].toString())
        .attr('r', (d => _this.textRScale(d['timeRange'].length)))
        .style('fill', (d) => globalConfig.PhraseColor[globalConfig.PhraseMapping[d['type']]])
        .on('click', function(d) {
            // console.log('text circle: ', d['text'])
            let timeRange = d['timeRange'][0]
            let startFrame = timeRange[0]*_this.fps
            let endFrame = timeRange[1]*_this.fps
            let frameSelection = [startFrame, endFrame]
            pipeService.emitUpdateTimeRange(frameSelection)
            pipeService.emitHighlightText(frameSelection)
        })


    points.append('text')
        .attr('class', (d) => 'circle-texts circle-text-'+d['phraseIdx'].toString())
        .each(function(d, i) {
            // compute the scale threshold for this element. i.e. how big does the scale need to be before I should display
            // console.log('cells: ', cells)
            // console.log('cells[i]: ', i, cells[i])
            if (cells[i] == undefined) return
            d.scaleThreshold = Math.sqrt(displayThreshold / d3.polygonArea(cells[i]))
            d.opacityScale = d3.scaleLinear()
                    .domain([d.scaleThreshold, d.scaleThreshold * 1.3])
                    .range([0, 1])

        })
        .attr('opacity', function(d) {
            if (d.scaleThreshold < 1) {
                return 1
            }
            return 0
        })
        .attr('x', (d => _this.textRScale(d['timeRange'].length)))
        .text((d) => d['text'])
        .style('font-size', '13px')
        .style('pointer-events', 'none')

    if (!$.isEmptyObject(_this.upperZoomedTransform)) {
        pointContainer.attr('transform', 'translate(' + _this.upperZoomedTransform.x + ',' + _this.upperZoomedTransform.y + ')scale(' + _this.upperZoomedTransform.k + ')')
        pointContainer.selectAll('circle').attr('transform', 'scale(' + (1/_this.upperZoomedTransform.k) + ')')
        pointContainer.selectAll('text')
            .attr('transform', 'scale(' + (1/_this.upperZoomedTransform.k) + ')')
            .attr('opacity', function(d) {
                if (_this.upperZoomedTransform.k > d.scaleThreshold) {
                    return d.opacityScale(_this.upperZoomedTransform.k)
                }
                return 0
            })
    }

    
    _this.upperZoom = d3.zoom()
        .scaleExtent([0.5, 40])
        .extent([[0, 0], [this.upperWidth, this.upperHeight]])
        .on('zoom', upperZoomed)

    this.upperContainer
        .call(_this.upperZoom)

    function upperZoomed () {

        // let new_xScale = d3.event.transform.rescaleX(_this.textXScale)
        // let new_yScale = d3.event.transform.rescaleY(_this.textYScale)

        // _this.textXScaleZoom = new_xScale
        // _this.textYScaleZoom = new_yScale
        
        _this.textNewZoomXScale = d3.event.transform.rescaleX(_this.textXScale)
        _this.textNewZoomYScale = d3.event.transform.rescaleY(_this.textYScale)

        // points.data(clusterPhraseData)
        //     .attr('transform', (d) => 'translate(' + new_xScale(d['textPosition'][0]) + ', ' + new_yScale(d['textPosition'][1]) + ')')
        let transform = d3.event.transform
        _this.upperZoomedTransform = transform
        // console.log('in upperZoomed _this.upperZoomedTransform: ', _this.upperZoomedTransform)

        pointContainer.attr('transform', 'translate(' + transform.x + ',' + transform.y + ')scale(' + transform.k + ')')

        pointContainer.selectAll('circle').attr('transform', 'scale(' + (1/transform.k) + ')')

        pointContainer.selectAll('text')
            .attr('transform', 'scale(' + (1/transform.k) + ')')
            .attr('opacity', function(d) {
                if (transform.k > d.scaleThreshold) {
                    return d.opacityScale(transform.k)
                }
                return 0
            })

        _this.drawLinks(_this.linkPointData)
    }
}

DrawRelation.prototype.drawGestureCluster = function (clusterPhraseData) {
    // draw projection   
    const _this = this 
    _this.lowerContainer.selectAll('g').remove()
    let pointContainer = _this.lowerContainer.selectAll('g')

    _this.lowerContainer.on('click' , function () {
        d3.select('#my_custom_menu')
            .style('display', 'none')
    })

    // console.log('clusterPhraseData: ', clusterPhraseData)
    let gestureClusterData = []
    for (let i=0; i<clusterPhraseData.length; i++) {
        for (let j=0; j<clusterPhraseData[i]['gesturePositionList'].length; j++) {
            if (_this.legendFilter.hasOwnProperty(clusterPhraseData[i]['timeRangeGestureType'][j])) {
                gestureClusterData.push({
                    'gesturePosition': clusterPhraseData[i]['gesturePositionList'][j],
                    'phraseIdx': clusterPhraseData[i]['phraseIdx'],
                    'gestureIdx': j,
                    'jointData': clusterPhraseData[i]['timeRangeJointData'][j],
                    'textPosition': clusterPhraseData[i]['textPosition'],
                    'gestureType': clusterPhraseData[i]['timeRangeGestureType'][j],
                    'gestureChange': clusterPhraseData[i]['gestureDistanceChange'][j],
                    'timeRange': clusterPhraseData[i]['timeRange'][j]
                })
            }
        }
    }

    // console.log('gestureClusterData: ', gestureClusterData)

    let points = pointContainer.data(gestureClusterData)
        .enter()
        .append('g')
        .attr('class', 'gestureGlyph')
        .attr('transform', (d) => 'translate(' + _this.gestureXScaleZoom(d['gesturePosition'][0]) + ', ' + _this.gestureYScaleZoom(d['gesturePosition'][1]) + ')')

    points.each(drawGlyph)
        
    _this.lowerZoom = d3.zoom()
        .scaleExtent([0.5, 40])
        .extent([[0, 0], [this.lowerWidth, this.lowerHeight]])
        .on('zoom', lowerZoomed)

    this.lowerContainer
        .call(_this.lowerZoom)

    function lowerZoomed () {
        // let new_xScale = d3.event.transform.rescaleX(_this.gestureXScale)
        // let new_yScale = d3.event.transform.rescaleY(_this.gestureYScale)
        // _this.gestureXScaleZoom = new_xScale
        // _this.gestureYScaleZoom = new_yScale

        _this.gestureNewZoomXScale = d3.event.transform.rescaleX(_this.gestureXScale)
        _this.gestureNewZoomYScale = d3.event.transform.rescaleY(_this.gestureYScale)


        points.data(gestureClusterData)
            .attr('transform', (d) => 'translate(' + _this.gestureNewZoomXScale(d['gesturePosition'][0]) + ', ' +  _this.gestureNewZoomYScale(d['gesturePosition'][1]) + ')')

        _this.drawLinks(_this.linkPointData)
    }


    function drawGlyph(glyphData) {
        let glyphContainer = d3.select(this)

        // console.log('glyphContainer.data(): ', glyphContainer.data())

        // let averageJoint = glyphData['averageJoint']
        let jointData = glyphData['jointData']
        let radius = 16

        if (jointData.length==0) return

        glyphContainer
            .append('circle')
            .attr('class', `lasso-circles gesture-circles gesture-circle-${glyphData['phraseIdx']} gesture-circle-${glyphData['phraseIdx']}-${glyphData['gestureIdx']}` )
            .attr('cx', 0)
            .attr('cy', 0)
            .attr('r', radius)
            .style('fill', globalConfig.gestureTypeColor[glyphData['gestureType']])
            .style('fill-opacity', 0.2)
            .style('stroke', 'black')
            .style('stroke-opacity', 0.1)
            .on('click', function(d) {
                let timeRange = d['timeRange']
                let startFrame = timeRange[0]*_this.fps
                let endFrame = timeRange[1]*_this.fps
                let frameSelection = [startFrame, endFrame]
                pipeService.emitUpdateTimeRange(frameSelection)
                pipeService.emitHighlightText(frameSelection)
            })
            .on('contextmenu', function(d) {
                d3.select('#my_custom_menu')
                  .style('position', 'absolute')
                  .style('left', (d3.event.pageX-10) + "px")
                  .style('top', (d3.event.pageY-95) + "px")
                  .style('display', 'block')
                
                _this.curGestureClusterData = d

                d3.event.preventDefault()
            })

        
        let gestureChangeTmp = glyphData['gestureChange']
        let gestureChange = []
        for (let i=0; i<gestureChangeTmp.length-1; i++) {
            let interpolateValue = d3.interpolateNumber(gestureChangeTmp[i], gestureChangeTmp[i+1])
            for (let j=0; j<1; j+=0.05) {
                gestureChange.push(interpolateValue(j))
            }
        }

        // console.log('gestureChange: ', gestureChange)
        

        // gestureChange
        let angleScale = d3.scaleLinear().domain([0, gestureChange.length-1]).range([0, 2*Math.PI])
        
        // define the area
        let area = d3.areaRadial()
            .angle(function(d, i) { 
                return angleScale(i) 
            })
            // .innerRadius()
            .innerRadius(function(d) { return radius-0.5-_this.distanceChangeScale(d) })
            .outerRadius(radius-0.5) 
            
            // .curve(d3.curveMonotoneX)

        // // define the line
        // let valueline = d3.lineRadial()
        //     .angle(function(d, i) { return angleScale(i); })
        //     .radius(function(d) { return y(d.close); });

        glyphContainer
            .append('path')
            .attr('class', 'gestureChange')
            .data([gestureChange])
            .attr('class', 'area')
            .attr('d', area)
            .style('fill', 'green')
            .style('opacity', 0.8)

        
        let stickmanGraph = glyphContainer.append('g')
            .attr('class', 'stickman-glyph')
            .attr('transform', 'translate(' + (-radius) + ', ' + (-radius) + ')')
       
        // // console.log('jointData: ', jointData)
        stickmanGraph.selectAll('.oneStickman')
            .data(jointData)
            .enter()
            .append('g')
            .attr('class', 'oneStickman')
            .each(drawOneStickman)
        
        function drawOneStickman(oneStickmanData) {   
            d3.select(this).append('g')
                .attr('class', 'body-point-connection')
                .selectAll('line')
                .data(globalConfig.UPBODYPairs)
                .enter()
                .append('line')
                .attr('x1', d => _this.gestureSpaceScale(oneStickmanData[d[0]*3+0]))
                .attr('y1', d => _this.gestureSpaceScale(oneStickmanData[d[0]*3+1]))
                .attr('x2', d => _this.gestureSpaceScale(oneStickmanData[d[1]*3+0]))
                .attr('y2', d => _this.gestureSpaceScale(oneStickmanData[d[1]*3+1]))
                .style('stroke', '#bdbdbd')
                .style('stroke-width', 1.5) // original one is 4
                .style('pointer-events', 'none')
        }
    }

}

DrawRelation.prototype.filterClusterWordIdxList = function (wordPhraseData, timeFilter, numFilter, legendFilter) {
    let clusterWordIdxList = []
    let totalTime = this.totalFrameNum/this.fps
    let timeStart = totalTime*timeFilter[0]
    let timeEnd = totalTime*timeFilter[1]
    let numStart = numFilter[0]
    let numEnd = numFilter[1]
    
    for (let i=0; i<wordPhraseData.length; i++) {
        let phraseTimeStart = wordPhraseData[i]['timeRange'][0][0]
        let phraseTimeEnd = wordPhraseData[i]['timeRange'][0][1]
        let phraseNum = wordPhraseData[i]['timeRange'].length
        let phraseType = wordPhraseData[i]['type']

        if (timeStart<= phraseTimeStart && 
            phraseTimeEnd <= timeEnd && 
            numStart<= phraseNum && 
            phraseNum<= numEnd && 
            legendFilter.hasOwnProperty(phraseType)) {
                clusterWordIdxList.push(wordPhraseData[i]['phraseIdx'])
            } 
    }
    return clusterWordIdxList
}

DrawRelation.prototype.drawLasso = function (lassoData) {
    const _this = this
    let circles = lassoData['circles']
    // let rScale = lassoData['rScale']
    // let radius = lassoData['radius']
    // Lasso functions
    let lasso_start = function() {
        lasso.items()
            // .attr('r', (d) => rScale(d[2])) // reset size
            .classed('not_possible',true)
            .classed('selected',false)

        _this.linksContainer.selectAll('path').remove()
    }

    let lasso_draw = function() {
        // Style the possible dots
        lasso.possibleItems()
            .classed('not_possible',false)
            .classed('possible',true)

        // Style the not possible dot
        lasso.notPossibleItems()
            .classed('not_possible',true)
            .classed('possible',false)
    }

    let lasso_end = function() {

        // Reset the color of all dots
        lasso.items()
            .classed('not_possible',false)
            .classed('possible',false)

        // Style the selected dots
        lasso.selectedItems()
            .classed('selected',true)
            .style('stroke', 'red')
            // .style('opacity', 0.2)
            .style('stroke-opacity', 1)
            .style('stroke-width', '1px')
            // .style('fill', '#EC888C')
            // .attr('r', (d) => rScale(d[2])*2)

        // Reset the style of the not selected dots
        lasso.notSelectedItems()
            .filter('.text-circles')
            .style('stroke-width', '0px')
            // .style('fill', (d) => globalConfig.PhraseColor[globalConfig.PhraseMapping[d['type']]])

        lasso.notSelectedItems()
            .filter('.gesture-circles')
            .style('stroke-opacity', 0.1)
            .style('stroke', 'black')
            // .style('fill', 'none')
            // .style('fill', 'black')
            // .attr('r', (d) => rScale(d[2]))
        
        d3.selectAll('.circle-texts')
            .style('fill', 'black')
            .style('font-weight', 300)
            

        // console.log('lasso.selectedItems(): ', lasso.selectedItems().data())
        // console.log('lasso.selectedItems: ', lasso.selectedItems().filter('.text-circles').data())

        let phraseIdxDict = {}
        let linkPointData = []
        let selectedLassoData = lasso.selectedItems().data()
        // console.log('selectedLassoData: ', selectedLassoData)

        for (let i=0; i<selectedLassoData.length; i++) {
            // let pointIdx = selectedLassoData[i][3]
            let phraseIdx = selectedLassoData[i]['phraseIdx']
            if (!phraseIdxDict.hasOwnProperty(phraseIdx)) {
                phraseIdxDict[phraseIdx] = 1
                linkPointData.push(selectedLassoData[i])
            } else {
                phraseIdxDict[phraseIdx] += 1
            }

            d3.select(`.text-circle-${phraseIdx}`)
                .style('stroke', 'red')
                .style('stroke-width', '1px')

            d3.select(`.circle-text-${phraseIdx}`)
                // .style('fill', 'red')
                .style('font-weight', '350')
            
            if (selectedLassoData[i].hasOwnProperty('gestureIdx')) {
                d3.select(`.gesture-circle-${phraseIdx}-${selectedLassoData[i]['gestureIdx']}`)
                .style('stroke', 'red')
                .style('stroke-width', '1px')
                .style('stroke-opacity', 1)
                
            } else {
                d3.select(`.gesture-circle-${phraseIdx}`)
                .style('stroke', 'red')
                .style('stroke-width', '1px')
                .style('stroke-opacity', 1)
            }
            
        }

        _this.linkPointData = linkPointData
        _this.drawLinks(linkPointData)
    }

    let lasso = d3Lasso.lasso()
            .closePathSelect(true)
            .closePathDistance(100)
            .items(circles)
            .targetArea(this.svg)
            .on('start',lasso_start)
            .on('draw',lasso_draw)
            .on('end',lasso_end)

    this.svg.call(lasso)
}


DrawRelation.prototype.drawLinks = function (linkPointData) {
    const _this = this
    _this.linksContainer.selectAll('*').remove()
    let sourceTargetData = []
        for (let i=0; i<linkPointData.length; i++) {
            let gesturePositionList = []
            let gestureTypeList = []
            if (linkPointData[i].hasOwnProperty('gesturePositionList')) {
                gesturePositionList = linkPointData[i]['gesturePositionList']
                gestureTypeList = linkPointData[i]['timeRangeGestureType']
            } else {
                gesturePositionList = [linkPointData[i]['gesturePosition']]
                gestureTypeList = [linkPointData[i]['gestureType']]
            }
            
            for (let j=0; j<gesturePositionList.length; j++) {
                if (_this.legendFilter.hasOwnProperty(gestureTypeList[j])) {
                    let source = {
                        // x: _this.textXScaleZoom(linkPointData[i]['textPosition'][0]),
                        // y: _this.textYScaleZoom(linkPointData[i]['textPosition'][1])
                        x: _this.textNewZoomXScale(linkPointData[i]['textPosition'][0]),
                        y: _this.textNewZoomYScale(linkPointData[i]['textPosition'][1])
                        
                    }
                    let target = {
                        // x: _this.gestureXScaleZoom(gesturePositionList[j][0]),
                        // y: _this.gestureYScaleZoom(gesturePositionList[j][1]) + _this.upperHeight+_this.betweenHeight
                        x: _this.gestureNewZoomXScale(gesturePositionList[j][0]),
                        y: _this.gestureNewZoomYScale(gesturePositionList[j][1]) + _this.upperHeight+_this.betweenHeight
                    }

                    sourceTargetData.push({
                        source: source,
                        target: target
                    })
                }
            }
        }
        
    _this.linksContainer.selectAll('path')
        .data(sourceTargetData)
        .enter()
        .append('path')
        .attr('class', 'linkPaths')
        .attr('fill', 'none')
        .attr('stroke', '#6baed6')
        .attr('stroke-width', '0.5px')
        .attr('d', _this.linkVertical)

}


DrawRelation.prototype.gestureType = function (gesture) {
    // [[x, y], [x, y], ...], 8
    // ['open', 'closed', 'others']
    let gestureType = ''

    // left shoulder 5; right shoulder 2;
    let leftShoulder = gesture[5]
    let rightShoulder = gesture[2]

    // left hand: 7; right hand: 4
    let leftHand = gesture[7]
    let rightHand = gesture[4]

    // // left elbow: 6; right elbow: 3
    // let leftElbow = gesture[6]
    // let rightElbow = gesture[3]

    // Euclidean distance
    let handDistance = Math.hypot(leftHand[0]-rightHand[0], leftHand[1]-rightHand[1])
    let shoulderDistance = Math.hypot(leftShoulder[0]-rightShoulder[0], leftShoulder[1]-rightShoulder[1])

    if (handDistance > 1.3*shoulderDistance) {
        gestureType = 'open'
    } else if (handDistance < 0.7*shoulderDistance) {
        gestureType = 'closed'
    } else {
        gestureType = 'others'
    }

    return gestureType
}

DrawRelation.prototype.gestureDistance = function (gestureA, gestureB) {
    // [x,y,prob]*9
    let distance = 0
    for (let i=0; i<8; i++) {
        distance += (Math.pow((gestureA[i*3+0]-gestureB[i*3+0]), 2)+Math.pow((gestureA[i*3+1]-gestureB[i*3+1]), 2) )
    }
    return distance
}

DrawRelation.prototype.updateBookmark = function () {
    this.bookmarkContainer.selectAll('*').remove()
    const _this = this

    let rows = this.bookmarkContainer.selectAll('div')
        .data(this.bookmarkData)
        .enter()
        .append('div')
        .attr('class', (_, i) => 'bookmark-row-'+i)
        .style('width', '330px')
        .style('height', '80px')
        // .style('background', '#F9E6E6')
        .style('margin', '2px')
        .style('border-bottom', 'dashed')
        .style('border-width', '1px')

    let columnData = ['gesture', 'annotation', 'action']

    let columnWidth = {
        'gesture': Math.round(0.4*330),
        'annotation': Math.round(0.4*330),
        'action': Math.round(0.2*330)
    }

    rows.selectAll('div')
        .data(columnData)
        .enter()
        .append('div')
        .attr('class', (d) => d)
        .style('width', (d) => columnWidth[d]+'px')
        .style('height', '80px')
        .style('display', 'inline-block')
        .style('position', 'relative')
        .style('left', '0px')
        .style('top', function(d) {
            if (d == 'gesture') {
                return '-6px'
            } else if (d == 'annotation'){
                return '-0px'
            } else {
                return '-30px'
            }
        })
    
    let gestureSvgHeight = 80
    let gestureSvgWidth = columnWidth['gesture']
    let gestureWidth = 60
    let gestureHeight = 60
    let gestureMargin = {
        top: (gestureSvgHeight-gestureHeight)/2,
        right: (gestureSvgWidth-gestureWidth)/2,
        bottom: (gestureSvgHeight-gestureHeight)/2,
        left: (gestureSvgWidth-gestureWidth)/2
    }

    let gestureSpaceScale = d3.scaleLinear()
        .domain([0, 1])
        .range([0, gestureWidth])

    function drawGlyph(glyphData) {
        let glyphContainer = d3.select(this).select('.gestureSvg')
            .append('g')
            .attr('transform', 'translate(' + (gestureMargin.left+gestureWidth/2) + ', ' + (gestureMargin.top+gestureHeight/2) + ')')

        let jointData = glyphData['jointData']
        let radius = gestureWidth/2

        if (jointData.length==0) return

        glyphContainer
            .append('circle')
            .attr('class', `lasso-circles gesture-circles gesture-circle-${glyphData['phraseIdx']} gesture-circle-${glyphData['phraseIdx']}-${glyphData['gestureIdx']}` )
            .attr('cx', 0)
            .attr('cy', 0)
            .attr('r', radius)
            .style('fill', globalConfig.gestureTypeColor[glyphData['gestureType']])
            .style('fill-opacity', 0.1)
            .style('stroke', 'black')
            .style('stroke-opacity', 0.1)
            .on('click', function() {
                let timeRange = glyphData['timeRange']
                let startFrame = timeRange[0]*_this.fps
                let endFrame = timeRange[1]*_this.fps
                let frameSelection = [startFrame, endFrame]
                pipeService.emitUpdateTimeRange(frameSelection)
                pipeService.emitHighlightText(frameSelection)
            })
        
        let gestureChangeTmp = glyphData['gestureChange']
        let gestureChange = []
        for (let i=0; i<gestureChangeTmp.length-1; i++) {
            let interpolateValue = d3.interpolateNumber(gestureChangeTmp[i], gestureChangeTmp[i+1])
            for (let j=0; j<1; j+=0.05) {
                gestureChange.push(interpolateValue(j))
            }
        }
        
        // gestureChange
        let angleScale = d3.scaleLinear().domain([0, gestureChange.length-1]).range([0, 2*Math.PI])
        
        // define the area
        let area = d3.areaRadial()
            .angle(function(d, i) { 
                return angleScale(i) 
            })
            // .innerRadius()
            .innerRadius(function(d) { return radius-0.5-_this.distanceChangeScale(d) })
            .outerRadius(radius-0.5) 

        glyphContainer
            .append('path')
            .attr('class', 'gestureChange')
            .data([gestureChange])
            .attr('class', 'area')
            .attr('d', area)
            .style('fill', 'green')
            // .style('opacity', 0.2)

        
        let stickmanGraph = glyphContainer.append('g')
            .attr('class', 'stickman-glyph')
            .attr('transform', 'translate(' + (-radius) + ', ' + (-radius) + ')')
       
        // // console.log('jointData: ', jointData)
        stickmanGraph.selectAll('.oneStickman')
            .data(jointData)
            .enter()
            .append('g')
            .attr('class', 'oneStickman')
            .each(drawOneStickman)
        
        function drawOneStickman(oneStickmanData) {   
            d3.select(this).append('g')
                .attr('class', 'body-point-connection')
                .selectAll('line')
                .data(globalConfig.UPBODYPairs)
                .enter()
                .append('line')
                .attr('x1', d => gestureSpaceScale(oneStickmanData[d[0]*3+0]))
                .attr('y1', d => gestureSpaceScale(oneStickmanData[d[0]*3+1]))
                .attr('x2', d => gestureSpaceScale(oneStickmanData[d[1]*3+0]))
                .attr('y2', d => gestureSpaceScale(oneStickmanData[d[1]*3+1]))
                .style('stroke', '#bdbdbd')
                .style('stroke-width', 1.5) // original one is 4
                .style('pointer-events', 'none')
        }
    }

    // gesture
    rows.selectAll('div.gesture')
        .append('svg')
        .attr('class', 'gestureSvg')
        .attr('width', gestureSvgWidth)
        .attr('height', gestureSvgHeight)
        // .append('rect')
        // .attr('x', 0)
        // .attr('y',0)
        // .attr('width', 100)
        // .attr('height', 70)
        // .style('fill', 'red')

    rows.each(drawGlyph)

    // annotation
    rows.selectAll('div.annotation')
        .append('div')
        .style('position', 'relative')
        .style('top', '15px')
        .append('textarea')
        .attr('x', '10px')
        .attr('y', '10px')
        .style('width', (columnWidth['annotation']-20)+'px')
        .style('height', '50px')
        .style('font-size', '12px')
        // .text(function (d) {
        //     console.log('text: ', d)
        //     return d
        // })

    rows.each(function(gestureData) {
        d3.select(this).select('textarea').text(gestureData['gestureType'])
    })


    // delete
    rows.selectAll('div.action')
        .append('div')
        .style('position', 'relative')
        .style('top', '25px')
        .html('<button><span class="fa fa-trash"></span></button>')

    rows.each(function(gestureData) {
        d3.select(this).select('button')
            .on('click', function() {
                for (let i=0; i<_this.bookmarkData.length; i++) {
                    if (_this.bookmarkData[i]['gestureIdx'] == gestureData['gestureIdx']) {
                        _this.bookmarkData.splice(i, 1)
                    }
                }
                _this.updateBookmark()
            })
    })
}

export default DrawRelation
