/* global d3 $ */
import videojs from 'video.js'
import pipeService from '../../service/pipeService.js'
import globalConfig from '../../service/globalConfig.js'
// import dataService from '../../service/dataService.js'

let DrawVideo = function (id) {
    this.svgWidth = $(id).width()
    this.svgHeight = $(id).height()

    this.refreshIntervalId = -1
    this.refreshAnimation = -1
    let formatTime = d3.timeFormat('%M:%S')
    this.formatMinutes = function(d) { return formatTime(new Date(2017, 0, 1, 0, 0, d)) }

    this.setupVideo('my-player')

    // d3.select('#cameraButton').on('click', function() {
    //     console.log('screenshot: ')
    // })

    this.screenshotList = []
    this.screenshotIdx = 0
    const _this = this

    let video = ''
    let output = ''
    var scale = 0.2
    let initialize = function() {
        output = d3.select("#videoScreenshot")
            .append('div')
            .style('width', '333px')
            .style('height', '114px')
            .style('overflow', 'scroll')
            .style('float', 'left')
            .style('white-space', 'nowrap')
            // .style('overflow-x', 'scroll')
            // .style('overflow-y', 'hidden')

        video = $("#my-player_html5_api").get(0)
        $("#cameraButton").click(captureImage)
    }

    let captureImage = function() {
        // console.log('captureImage:')
    
        let img = new Image()
        // img.crossOrigin = "anonymous";
        img.crossOrigin = '*'
        let dataURL = ''
        let canvas = document.createElement("canvas");
        canvas.width = video.videoWidth * scale;
        canvas.height = video.videoHeight * scale;
        canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
        dataURL = canvas.toDataURL('image/jpeg');
        img.src = dataURL
        // console.log('dataURL: ', dataURL)
        // output.prepend(img)

        let imageContainer = output.append('div')
            .attr('id', 'screenshot-'+_this.screenshotIdx)
            .attr('class', 'imageContainer')
            // .style('width', canvas.width+'px')
            // .style('height', canvas.height+'px')
            .style('margin', '15px 5px 10px 5px')
            .style('position', 'relative')
            .style('display', 'inline-block')
        
        imageContainer.append('img')
            .attr('id', 'img-'+_this.screenshotIdx)
            .style('width', video.videoWidth*0.08+'px')
            .style('height', video.videoHeight*0.08+'px')
            .attr('src', dataURL)
            .on('click', function () {
                // console.log('d3.select(this): ', d3.select(this))
                // console.log('d3.select(this), img: ', d3.select(this)['_groups'][0])
                // console.log('d3.select(this), img[0]: ', d3.select(this)['_groups'][0][0])
                let imgId = d3.select(this)['_groups'][0][0]['id']
                let screenshotIdx = imgId.split('-')[1]
                // console.log('screenshotIdx: ', screenshotIdx)
                // console.log('_this.screenshotList: ', _this.screenshotList)

                for (let i=0; i<_this.screenshotList.length; i++) {
                    if (_this.screenshotList[i]['idx'] == screenshotIdx) {
                        _this.myPlayer.currentTime(_this.screenshotList[i]['curTime'])
                    }
                }
            })

        imageContainer.append('img')
            .attr('id', 'delete-'+_this.screenshotIdx)
            // .attr('src', `${dataService.dataServerUrl}/deleteImage`)
            .attr('src', `/image/delete.png`)
            .attr('width', 20)
            .attr('height', 20)
            .style('position', 'absolute')
            .style('right', '-5px')
            .style('top', '-5px')
            .style('opacity', 0.0)
            .style('pointer-events', 'all')
            .on('mouseover', function () {
                // console.log('d3.select(this):',  d3.select(this))
                d3.select(this).style('opacity', 1.0)
            })
            .on('mouseout', function () {
                // console.log('d3.select(this):',  d3.select(this))
                d3.select(this).style('opacity', 0)
            })
            .on('click', function() {
                d3.select(this.parentNode).remove()
                d3.select(this).remove()
            })

        let curTime = _this.myPlayer.currentTime()
        _this.curTimeToLabeledFrame(curTime)
        let curFrame = _this.frameIdxList[_this.arrayIdx]

        _this.screenshotList.push({'dataURL': dataURL, 'idx': _this.screenshotIdx, 'frame': curFrame, 'curTime': curTime})
        _this.screenshotIdx += 1
        
        imageContainer.append('text')
            .style('position', 'absolute')
            .style('left', '38px')
            .style('bottom', '-20px')
            .style('font-size', '12px')
            .style('text-anchor', 'middle')
            .text(_this.formatMinutes(curTime))

        let matchWord = ''
        // console.log('curTime: ', curTime)
        for (let i=0; i<_this.textData.length; i++) {
            // console.log('_this.textData[i][word]: ', _this.textData[i]['word'])
            if (_this.textData[i]['type'] == 'punctuation') {
                continue
            }
            if (curTime<parseFloat(_this.textData[i]['start_time'])) {
                break
            }
            if (curTime>=parseFloat(_this.textData[i]['start_time']) && curTime<=parseFloat(_this.textData[i]['end_time'])) {
                matchWord = _this.textData[i]['word']
                break
            }
        }

        // console.log('matchWord: ', matchWord)

        
        imageContainer.append('text')
            .style('position', 'absolute')
            .style('left', `${50-matchWord.length*1.8}px`)
            .style('bottom', '-35px')
            .style('font-size', '12px')
            .attr('text-anchor', 'middle')
            .text(matchWord)

    }

    initialize()
}


DrawVideo.prototype.setupVideo = function (id) {
    const _this = this
    this.myPlayer = videojs(id, {
        controls: true,
        autoplay: false,
        preload: 'auto',
        language: 'en',
        playbackRates: [0.2, 0.5, 1.0, 1.5, 2.0, 3.0, 4.0, 5.0],
        inactivityTimeout: 0, // Note: showing the control bars once the video is loaded.
        height: this.svgHeight,
        width: this.svgWidth,
        // sources: [{
        //     src: url,
        //     type: 'video/mp4'
        //   }]
    })

    pipeService.onSeekFromPhase((phase) => {
        // console.log('phase: ', phase)
        this.startTime = this.seekTimeList[phase['startFrameIdx']]
        this.endTime = this.seekTimeList[phase['endFrameIdx']]
        // console.log('start: ', this.startTime)
        // console.log('end: ', this.endTime)
        this.myPlayer.currentTime(this.startTime)

        this.myPlayer.on('timeupdate', function () {
            if (_this.myPlayer.currentTime() >= _this.endTime) {
                _this.myPlayer.pause()
                _this.endTime = _this.seekTimeList[_this.seekTimeList.length-1]
            }
        })
        this.myPlayer.play()
    })

    pipeService.onClickFromTextlist((timeRange) => {
        this.startTime = this.seekTimeList[timeRange[0]-5]
        this.endTime = this.seekTimeList[timeRange[1]+5]
        // console.log('start: ', this.startTime)
        // console.log('end: ', this.endTime)
        this.myPlayer.currentTime(this.startTime)

        this.myPlayer.on('timeupdate', function () {
            if (_this.myPlayer.currentTime() >= _this.endTime) {
                _this.myPlayer.pause()
                _this.endTime = _this.seekTimeList[_this.seekTimeList.length-1]
            }
        })
        this.myPlayer.play()
    })


    pipeService.onPlayVideoFromSlider((info) => {
        let frameSelection = info['frameSelection']
        if (info['status'] == 'pause') {
            this.myPlayer.pause()
        } else {
            let curTime = _this.myPlayer.currentTime()
            _this.curTimeToLabeledFrame(curTime)
            let curFrame = _this.frameIdxList[_this.arrayIdx]
            if (frameSelection[0]<=curFrame && curFrame<=frameSelection[1]) {
                this.myPlayer.play()
            } else {
                let bisect = d3.bisector(function(d) { return d }).left
                curFrame = frameSelection[0]
                let arrayIdx = bisect(this.frameIdxList, curFrame)
                this.seekTime = this.seekTimeList[arrayIdx]
                this.arrayIdx = arrayIdx
                this.myPlayer.currentTime(this.seekTime)
                this.myPlayer.play()
            }
        }
    })

    pipeService.onVideoFrameFromSlider((curFrame) => {
        let bisect = d3.bisector(function(d) { return d }).left
        let arrayIdx = bisect(this.frameIdxList, curFrame)
        this.seekTime = this.seekTimeList[arrayIdx]
        this.arrayIdx = arrayIdx
        this.myPlayer.currentTime(this.seekTime)
    })

    // console.log('videoHeight, videoWdith: ', this.videoHeight, this.videoWidth)
    this.myPlayer.ready(function () {
        // console.log('myPlayer is ready: ', _this.myPlayer)
        _this.myPlayer.pause()
        _this.myPlayer.currentTime(0)

        _this.myPlayer.on('ended', function () {
        })

        _this.myPlayer.on('pause', function () {
            // clear the timer
            clearInterval(_this.refreshIntervalId)
            clearInterval(_this.refreshAnimation)

            let curTime = _this.myPlayer.currentTime()
            _this.curTimeToLabeledFrame(curTime)
            _this.updateDrawingLabelPoses(_this.arrayIdx)
        })

        _this.myPlayer.on('play', function () {
            // console.log('_this.myPlayer.currentTime(): ', _this.myPlayer.currentTime())
            if (_this.myPlayer.currentTime() > _this.endTime) {
                _this.myPlayer.pause()
            }
            _this.removeDrawingLabelPoses()

            // set up timer for updating the frameLine
            _this.refreshIntervalId = setInterval(() => {
                let curTime = _this.myPlayer.currentTime()
                _this.curTimeToLabeledFrame(curTime)
                pipeService.emitFrameSelectedFromVideo(_this.arrayIdx)
            }, 500)


            _this.refreshAnimation = setInterval(() => {
                let curTime = _this.myPlayer.currentTime()
                _this.curTimeToLabeledFrame(curTime)
                pipeService.emitAnimationFrameFromVideo(_this.frameIdxList[_this.arrayIdx])
            }, 100)


        })

        _this.myPlayer.on('seeking', function () {
            let curTime = parseFloat(_this.myPlayer.currentTime().toFixed(2))
            if (curTime !== _this.seekTime) {
                _this.curTimeToLabeledFrame(curTime)
                _this.myPlayer.currentTime(_this.seekTime)  // add for solve the problem?
            }

            if (_this.myPlayer.paused()) {
                _this.updateDrawingLabelPoses(_this.arrayIdx)
            } else {
                _this.removeDrawingLabelPoses()
            }
            // console.log('curTime: ', curTime)
            // console.log('curArrayIdx: ', _this.arrayIdx)
        })
    })

    pipeService.onFrameSelectedFromTimeBar((arrayIdx) => {
        _this.arrayIdx = arrayIdx
        _this.seekFrame = _this.frameIdxList[arrayIdx]
        _this.seekTime = _this.seekTimeList[arrayIdx]
        _this.myPlayer.currentTime(_this.seekTime)
    })

    pipeService.onFrameSelectedFromAnimation((curFrame) => {
        let bisect = d3.bisector(function(d) { return d }).left
        let arrayIdx = bisect(this.frameIdxList, curFrame)
        this.seekTime = this.seekTimeList[arrayIdx]
        _this.arrayIdx = arrayIdx
        _this.myPlayer.currentTime(_this.seekTime)
    })
}

DrawVideo.prototype.updateVideo = function (url) {
    const _this = this
    this.myPlayer.src({type: 'video/mp4', src: url})

    _this.myPlayer.on('click', function () {
        if (_this.myPlayer.readyState() < 1) {
            // do not have metadata yet so loadedmetadata event not fired yet (I presume)
            // wait for loadedmetdata event
            _this.myPlayer.one('loadedmetadata', onLoadedMetadata)
        }
        else {
            // metadata already loaded
            onLoadedMetadata()
        }
    })
    
    function onLoadedMetadata() {
        _this.endTime = _this.myPlayer.duration()
    }

}

DrawVideo.prototype.layout = function (id, data, url) {
    // console.log('DrawVideo videoINfo: ', data)
    this.visionData = data
    this.videoInfo = this.visionData['videoInfo']
    this.poseData = this.visionData['poseData']
    this.faceData = this.visionData['faceData']
    this.textData = this.visionData['stickmanData']['textData']
    // console.log('this.textData: ', this.textData)
    // console.log('this.faceData: ', this.faceData)

    d3.select(id).selectAll('*').remove()
    this.videoHeight = this.videoInfo['videoHeight']
    this.videoWidth = this.videoInfo['videoWidth']
    this.fps = this.videoInfo['fps']

    // console.log('url: ', url)
    this.myPlayer.src({type: 'video/mp4', src: url})

    // layout
    let containerRatio = this.svgWidth / this.svgHeight
    let videoRatio = this.videoWidth / this.videoHeight

    if (videoRatio > containerRatio) {
        this.videoScreenWidth = this.svgWidth
        this.videoScreenHeight = this.svgWidth / videoRatio
    } else {
        this.videoScreenHeight = this.svgHeight
        this.videoScreenWidth = this.svgHeight * videoRatio
    }

    // setup label environment
    let xOffset = (this.svgWidth - this.videoScreenWidth) / 2
    let yOffset = (this.svgHeight - this.videoScreenHeight) / 2

    this.margin = { top: yOffset, right: xOffset, bottom: yOffset, left: xOffset }

    // console.log('this.margin: ', this.margin)

    this.svg = d3.select(id).append('svg')
        .attr('class', 'videoSvg')
        .attr('height', this.svgHeight)
        .attr('width', this.svgWidth)

    this.graphContainer = this.svg.append('g')
        .attr('class', 'g_main')
        .attr('transform', 'translate(' + this.margin.left + ', ' + this.margin.top + ')')

    // global variable in this file
    this.labeledFrameIndex = -1   // initialize it as -1 to indicate no updating

    this.rectBdHighlightClr = '#ff0000'
    this.rectBdNormalClr = '#00ff00'
    this.rectBdWarningClr = 'yellow' // when not sure about the computed baby id

    let sampleInterval = 1
    this.arrayIdx = 0
    this.seekTime = 0
    this.frameIdxList = d3.range(0, this.videoInfo['totalFrmNum'], sampleInterval)
    // console.log('this.frameIdxList: ', this.frameIdxList)
    this.seekTimeList = this.frameIdxList.map(d => parseFloat((d*1/this.fps).toFixed(2)))
    // console.log('this.seekTimeList: ', this.seekTimeList)

}

DrawVideo.prototype.removeDrawingLabelPoses = function () {
    this.graphContainer.selectAll('*').remove()
}

// eslint-disable-next-line
DrawVideo.prototype.updateDrawingLabelPoses = function(arrayIdx) {
    this.removeDrawingLabelPoses();
    const _this = this;
    // console.log('drawVideo arrayIdx: ', arrayIdx);
    pipeService.emitFrameSelectedFromVideo(arrayIdx)

    // console.log('arrayIdx: ', arrayIdx)
    // console.log('poseData: ', this.poseData[arrayIdx.toString()])

    if (this.faceData === undefined || this.faceData.length === 0) {
        // console.log('========= ')
        return
    }
    const frameInfo = this.faceData[arrayIdx]
    // console.log('drawVideo, frameInfo: ', frameInfo)
    // if (Object.keys(frameInfo).length === 0) {
    if (frameInfo.length === 0) {
        console.log('===========', arrayIdx)
    } else {
        this.graphContainer.append('g')
        .attr('class', 'peopleface')
        .selectAll('g')
        .data(frameInfo)
        .enter()
        .append('g')
        .attr('class', () => `person_${arrayIdx}`)
        .attr('transform', (person) => {
            // console.log('person: ', person)
            const x = person['position'][0] * _this.videoScreenWidth
            const y = person['position'][1] * _this.videoScreenHeight
            return `translate(${x}, ${y})`
        })
        .each(drawRect)
    }
    function drawRect(person) {
        const w = person['position'][2] * _this.videoScreenWidth
        const h = person['position'][3] * _this.videoScreenHeight
        d3.select(this)
            .append('rect')
            .attr('width', w)
            .attr('height', h)
            .style('fill', 'red')
            .style('fill-opacity', 0.0)
            .style('fill', 'pink')
            .style('stroke', _this.rectBdNormalClr)
            .style('stroke-width', 2) // original one is 4
        
        d3.select(this)
            .append('text')
            .attr('x', 0)
            .attr('y', -35)
            .attr('dy', '2em')
            .text(() => person['emotion'])
            .attr('font-family', 'sans-serif')
            .attr('font-size', '8px')
            .attr('fill', 'white')
            .style('text-anchor', 'left')
    }

    
    // draw poseInfo
    if (this.poseData === undefined || Object.keys(this.poseData).length === 0) {
        console.log(
            '====No pose for the whole video===== '
        ); /* eslint-disable-line */
        return;
    }
  
    let framePoseInfo = this.poseData[arrayIdx.toString()];
    let peoplePose = framePoseInfo['people']
    if (peoplePose.length === 0) {
        console.log('no people, arrayIdx: ', arrayIdx)
        return;
    }
  
    // prepare to draw the rectangles
    this.graphContainer
        .append('g')
        .attr('class', 'poseInfo')
        .selectAll('g')
        .attr('class', 'peoplePoseInfo')
        .data(peoplePose)
        .enter()
        .append('g')
        .attr('class', () => `people_${arrayIdx}`)
        .each(function drawPose(person) {
            // console.log('person: ', person)
            
            let bodyPointNum = 25
            let handPointNum = 21
            let personBodyPoints = person['pose_keypoints_2d']
            let personLeftHandPoints = person['hand_left_keypoints_2d']
            let personRightHandPoints = person['hand_right_keypoints_2d']
            let bodyPoints = []
            let leftHandPoints = []
            let rightHandPoints = []
            for (let i=0; i<bodyPointNum; i++) {
                bodyPoints.push([
                    personBodyPoints[3*i]/_this.videoWidth,
                    personBodyPoints[3*i+1]/_this.videoHeight,
                    personBodyPoints[3*i+2],
                ])
            }

            for (let i=0; i<handPointNum; i++) {
                leftHandPoints.push([
                    personLeftHandPoints[3*i]/_this.videoWidth,
                    personLeftHandPoints[3*i+1]/_this.videoHeight,
                    personLeftHandPoints[3*i+2],
                ])
                rightHandPoints.push([
                    personRightHandPoints[3*i]/_this.videoWidth,
                    personRightHandPoints[3*i+1]/_this.videoHeight,
                    personRightHandPoints[3*i+2],
                ])
            }
        
            // body points
            d3.select(this)
                .append('g')
                .attr('class', 'bodyPoints')
                .selectAll('cirlce')
                .data(bodyPoints.filter((d) => {
                    return d[2] !== 0 // filter no points
                }))
                .enter()
                .append('circle')
                .attr('cx', d => d[0] * _this.videoScreenWidth)
                .attr('cy', d => d[1] * _this.videoScreenHeight)
                .attr('r', 2)
                .style('fill', 'red');
    
            let bodyPointPairData = globalConfig.BODY25Pairs
                .map(pair => {
                    if (bodyPoints[pair[0]][2] !== 0 && bodyPoints[pair[1]][2] !== 0) {
                        return pair;
                    }
                    return null;
                })
                .filter(d => d);
    
            d3.select(this)
                .append('g')
                .attr('class', 'body-point-connection')
                .selectAll('line')
                .data(bodyPointPairData)
                .enter()
                .append('line')
                .attr('x1', d => bodyPoints[d[0]][0] * _this.videoScreenWidth)
                .attr('y1', d => bodyPoints[d[0]][1] * _this.videoScreenHeight)
                .attr('x2', d => bodyPoints[d[1]][0] * _this.videoScreenWidth)
                .attr('y2', d => bodyPoints[d[1]][1] * _this.videoScreenHeight)
                .style('stroke', '#bdbdbd')
                .style('stroke-width', 2); // original one is 4

            // left hand points
            d3.select(this)
                .append('g')
                .attr('class', 'leftHandPoints')
                .selectAll('cirlce')
                .data(leftHandPoints.filter((d) => {
                    return d[2] !== 0 // filter no points
                }))
                .enter()
                .append('circle')
                .attr('cx', d => d[0] * _this.videoScreenWidth)
                .attr('cy', d => d[1] * _this.videoScreenHeight)
                .attr('r', 2)
                .style('fill', globalConfig.leftHandColor);

        let leftHandPointPairData = globalConfig.handPairs
                .map(pair => {
                    if (leftHandPoints[pair[0]][2] !== 0 && leftHandPoints[pair[1]][2] !== 0) {
                        return pair;
                    }
                    return null;
                })
                .filter(d => d);

        d3.select(this)
                .append('g')
                .attr('class', 'lefthand-point-connection')
                .selectAll('line')
                .data(leftHandPointPairData)
                .enter()
                .append('line')
                .attr('x1', d => leftHandPoints[d[0]][0] * _this.videoScreenWidth)
                .attr('y1', d => leftHandPoints[d[0]][1] * _this.videoScreenHeight)
                .attr('x2', d => leftHandPoints[d[1]][0] * _this.videoScreenWidth)
                .attr('y2', d => leftHandPoints[d[1]][1] * _this.videoScreenHeight)
                .style('stroke', globalConfig.leftHandColor)
                .style('stroke-width', 2)  // original one is 4


        // right hand points
        d3.select(this)
            .append('g')
            .attr('class', 'rightHandPoints')
            .selectAll('cirlce')
            .data(rightHandPoints.filter((d) => {
                return d[2] !== 0 // filter no points
            }))
            .enter()
            .append('circle')
            .attr('cx', d => d[0] * _this.videoScreenWidth)
            .attr('cy', d => d[1] * _this.videoScreenHeight)
            .attr('r', 2)
            .style('fill', globalConfig.rightHandColor)
  
        let rightHandPointPairData = globalConfig.handPairs
            .map(pair => {
                if (rightHandPoints[pair[0]][2] !== 0 && rightHandPoints[pair[1]][2] !== 0) {
                    return pair;
                }
                return null;
            })
            .filter(d => d);
  
        d3.select(this)
            .append('g')
            .attr('class', 'body-point-connection')
            .selectAll('line')
            .data(rightHandPointPairData)
            .enter()
            .append('line')
            .attr('x1', d => rightHandPoints[d[0]][0] * _this.videoScreenWidth)
            .attr('y1', d => rightHandPoints[d[0]][1] * _this.videoScreenHeight)
            .attr('x2', d => rightHandPoints[d[1]][0] * _this.videoScreenWidth)
            .attr('y2', d => rightHandPoints[d[1]][1] * _this.videoScreenHeight)
            .style('stroke', globalConfig.rightHandColor)
            .style('stroke-width', 2); // original one is 4

    });
};


DrawVideo.prototype.curTimeToLabeledFrame = function (curTime) {
    let bisect = d3.bisector(function(d) { return d }).left
    let arrayIdx = bisect(this.seekTimeList, curTime)
    this.seekTime = this.seekTimeList[arrayIdx]
    this.arrayIdx = arrayIdx
    return { 'seekTime': this.seekTime }
}

export default DrawVideo
