export default {
    dataServerUrl,
    initialization,
    videoInfo,
    poseData,
    visionData,
    audioData,
    textData,
    saveGesturePhaseData,
    savePhaseLabelData,
    clusterData,
    savePhaseData,
    saveData,
    runClusterData,
    runPhraseClusterData
}
